<template>
    <div id="CustomGraphOptions" role="dialog" class="modal my-popups right fade" data-backdrop="static"
        data-keyboard="false" v-cloak>
        <div role="document" class="modal-dialog mw-1100">
            <div class="modal-content">
                <div class="modal-header mheader-user">
                    <button type="button" class="close" v-on:click="CloseGraphOptions()">
                        <em aria-hidden="true" class="fa fa-times"></em>
                    </button>
                    <h4 id="myModalLabel" class="modal-title">{{ $t("GraphSetting") }}</h4>
                    <span class="user-guide">
                        <a class="ancuserguide" data-toggle="collapse" href="javascript:;" role="button"
                            aria-expanded="false" aria-controls="collapseExample">{{ $t("GuideForUser") }}</a>
                        <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                            <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                <div class="con" v-html="$t('User_Guide_Graph_Option')"></div>
                            </div>
                        </div>
                    </span>
                </div>
                <div class="modal-body p-3">
                    <loader :is-visible="isLoading"></loader>
                    <div class="container-fluid tab-content">
                        <div class="row">
                            <div class="col-md-6">
                                <dynamic-form lang="en" ref="refGraphForm" :buttons="false" :schema="FormSchema"
                                    v-on:OnSubmit="onSubmit">
                                    <template v-slot:tgslot-chartlist="{ data }">
                                        <div class="user_type_choice coloredicons row">
                                            <div class="btn-group btn-group-toggle mb-2" data-toggle="buttons"
                                                v-for="(item, index) in chartTypes" :key="index">
                                                <label
                                                    v-bind:class="{ 'btn btn_cstm': true, 'active': (item.id == selectedChartTypeID) }"
                                                    v-bind:for="'cls_usertype' + index">
                                                    <input type="radio" v-bind:value="item.id" name="cls_usertype"
                                                        v-bind:id="'cls_usertype' + index" v-model="selectedChartTypeID" />
                                                    <span class="d-block pb-2">
                                                        <em :class="[item.chart_Type == 'Pie' ? 'fa fa-pie-chart bg-transparent' : '',
                                                        item.chart_Type == 'Bar' ? 'fa fa-bar-chart bg-transparent' : '',
                                                        item.chart_Type == 'Line' ? 'fa fa-line-chart bg-transparent' : '',
                                                        item.chart_Type == 'Horizontal_Bar' ? 'fa fa-bars bg-transparent' : '',
                                                        item.chart_Type == 'Donut' ? 'fa fa-circle-o-notch bg-transparent' : '']"
                                                            aria-hidden="true"></em>
                                                    </span>
                                                </label>
                                            </div>
                                        </div>
                                        <span class="invalid-feedback d-block" v-if="data.error != ''">{{ data.error }}</span>
                                    </template>
                                </dynamic-form>
                            </div>
                            <div class="col-md-6">
                                <div class="p-3 bg-light border">
                                    <div class="heading-border">
                                        <h5 class="h5"><span>{{ $t('Preview') }}</span></h5>
                                    </div>
                                    <span class="text-center my-3 w-100 float-left">{{ FormSchema[0].Data[0].value }} -
                                        <button href="javascript:;" class="btn btn-info mt-2" v-on:click="ViewReportGraph">
                                            {{ $t('ViewReport') }}
                                        </button>
                                    </span>
                                    <div v-if="PreviewChart" class="col-sm-12 col-lg-12 px-0 py-4 float-left bg-white">
                                        <VueApexCharts :type="selectedChartType" :options="chartOptions"
                                            :series="chartseries">
                                        </VueApexCharts>
                                        <div :class="selectedChartType == 'pie' || selectedChartType == 'donut' ? 'text-left pl-4 ml-4' : 'text-center'"
                                            v-if="aggregateFunctionResult != 0">
                                            <a href="javascript:;">{{ resultMsg }} : {{ aggregateFunctionResult }}</a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <!-- <div class="col-lg-12">
                                        <div class="form-group">
                                            <label>{{ $t('ViewAs') }}:</label>
                                            <div v-if="isTypeInteger" class="input-group">
                                                <input name="sum" v-model="aggregateFunctionType" value="Sum" type="radio"
                                                    class="mr-2" />{{ $t('Sum') }}
                                                <input name="sum" v-model="aggregateFunctionType" value="Avg" type="radio"
                                                    class="mr-2 ml-3" />{{ $t('Average') }}
                                                <input name="sum" v-model="aggregateFunctionType" value="Min" type="radio"
                                                    class="mr-2 ml-3" />{{ $t('Min') }}
                                                <input name="sum" v-model="aggregateFunctionType" value="Max" type="radio"
                                                    class="mr-2 ml-3" />{{ $t('Max') }}
                                                <input name="sum" v-model="aggregateFunctionType" value="Count" type="radio"
                                                    class="mr-2 ml-3" />{{ $t('Count') }}
                                            </div>
                                            <div class="input-group" v-if="!isTypeInteger">
                                                <input name="sum" v-model="aggregateFunctionType" value="Count" checked
                                                    type="radio" class="mr-2 ml-3" />{{ $t('Count') }}
                                            </div>
                                        </div>
                                    </div>
                        </div> -->
                            <!-- <div class="col-sm-12 col-lg-6 p-3 bg-light border">
                                <div class="heading-border">
                                    <h5 class="h5"><span>{{ $t('Preview') }}</span></h5>
                                </div>
                                <span class="text-center my-3 w-100 float-left">{{ reportTitle }} -
                                    <button href="#" class="btn btn-info" @click="ViewReportGraph">
                                        {{ $t('ViewReport') }}
                                    </button>
                                </span>
                                <div v-if="PreviewChart" class="col-sm-12 col-lg-12 px-0 py-4 float-left bg-white">
                                    <VueApexCharts :type="selectedChartType" :options="chartOptions" :series="chartseries">
                                    </VueApexCharts>
                                    <div :class="selectedChartType == 'pie' || selectedChartType == 'donut' ? 'text-left pl-4 ml-4' : 'text-center'"
                                        v-if="aggregateFunctionResult != 0">
                                        <a href="#">{{ resultMsg }} : {{ aggregateFunctionResult }}</a>
                                    </div>
                                </div>
                            </div> -->

                        </div>


                        <!-- <Form v-slot="{ values, meta }" ref="form">
                            <div class="row">
                                <div class="col-sm-12 col-lg-6 float-left leftsideicon pl-0 py-3 border">
                                    <div class="col-lg-12">
                                        <div class="form-group">
                                            <label>{{ $t('ReportTitle') }} :</label>
                                            <input v-model="reportTitle" placeholder="Report Title" class="form-control"
                                                type="text" maxlength="50" disabled />
                                        </div>
                                    </div>
                                    <div class="col-lg-12">
                                        <div class="form-group">
                                            <Field name="ChartTypes" :rules="{ 'required': true }" label="Chart Type"
                                                v-slot="{ field, errorMessage, meta }">
                                                <label>{{ $t('ChartTypes') }}<span class="text-danger">*</span></label>
                                                <div class="user_type_choice coloredicons row"
                                                    v-bind:class="{ '': true, 'is-invalid': meta.touched && !meta.valid && !selectedChartTypeID }">
                                                    <div class="btn-group btn-group-toggle mb-2" data-toggle="buttons"
                                                        v-for="(item, index) in chartTypes" :key="index">
                                                        <label
                                                            v-bind:class="{ 'btn btn_cstm': true, 'active': (item.id == selectedChartTypeID) }"
                                                            v-bind:for="'cls_usertype' + index">
                                                            <input v-bind="field" type="radio" v-bind:value="item.id"
                                                                name="cls_usertype" v-bind:id="'cls_usertype' + index"
                                                                v-model="selectedChartTypeID" />
                                                            <span class="d-block pb-2">
                                                                <em :class="[item.chart_Type == 'Pie' ? 'fa fa-pie-chart bg-transparent' : '',
                                                                item.chart_Type == 'Bar' ? 'fa fa-bar-chart bg-transparent' : '',
                                                                item.chart_Type == 'Line' ? 'fa fa-line-chart bg-transparent' : '',
                                                                item.chart_Type == 'Horizontal_Bar' ? 'fa fa-bars bg-transparent' : '',
                                                                item.chart_Type == 'Donut' ? 'fa fa-circle-o-notch bg-transparent' : '']"
                                                                    aria-hidden="true"></em>
                                                            </span>
                                                        </label>

                                                    </div>
                                                </div>
                                                <span name="ChartTypes" v-if="meta.touched && !meta.valid"
                                                    class="invalid-feedback">
                                                    {{ errorMessage }}
                                                </span>
                                            </Field>
                                        </div>
                                    </div>
                                    <div class="col-lg-12">
                                        <div class="form-group">
                                            <Field name="selectedChartXAxis" :rules="{ 'required': true }"
                                                v-model="selectedChartXAxis" label="X Axis"
                                                v-slot="{ field, errorMessage, meta }">
                                                <label>{{ $t('XAXIS') }}<span class="text-danger">*</span></label>
                                                <div class="input-group">
                                                    <select v-bind="field" @change="CheckFieldTypeX(selectedChartXAxis)"
                                                        v-bind:class="{ 'form-control': true, 'is-invalid': meta.touched && !meta.valid && !selectedChartXAxis }">
                                                        <option value="" disabled>Select</option>
                                                        <option v-for="item in Columnlist"
                                                            :value="item.CustomFieldId != '' ? item.CustomFieldId : item.FieldName"
                                                            :key="item.CustomFieldId">
                                                            {{ GetLocalizedValue((item.DisplayName != null &&
                                                                item.DisplayName != '') ?
                                                                item.DisplayName : item.FieldName) }}
                                                        </option>
                                                    </select>
                                                    <span name="selectedChartXAxis" v-if="meta.touched && !meta.valid"
                                                        class="invalid-feedback">{{
                                                            errorMessage
                                                        }}</span>
                                                </div>
                                            </Field>
                                        </div>
                                    </div>
                                    <div class="col-lg-12">
                                        <div class="form-group">
                                            <label>{{ $t('YAXIS') }}<span class="text-danger">*</span></label>
                                            <div class="input-group">
                                                <Field name="selectedChartYAxis" :rules="{ 'required': true }"
                                                    v-model="selectedChartYAxis" label="Y Axis"
                                                    v-slot="{ field, errorMessage, meta }">
                                                    <select v-bind="field" @change="CheckFieldType(selectedChartYAxis)"
                                                        :class="{ 'form-control': true, 'is-invalid': meta.touched && !meta.valid && !selectedChartYAxis }">
                                                        <option value="" disabled>Select</option>
                                                        <option
                                                            v-for="item in Columnlist.filter(x => x.CustomFieldId != selectedChartXAxis)"
                                                            :value="item.CustomFieldId != '' ? item.CustomFieldId : item.FieldName"
                                                            :key="item.CustomFieldId">
                                                            {{ GetLocalizedValue((item.DisplayName != null &&
                                                                item.DisplayName != '') ?
                                                                item.DisplayName : item.FieldName) }}
                                                        </option>
                                                    </select>
                                                    <span name="selectedChartYAxis" v-if="meta.touched && !meta.valid"
                                                        class="invalid-feedback">
                                                        {{ errorMessage }}
                                                    </span>
                                                </Field>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="col-lg-12">
                                        <div class="form-group">
                                            <label>{{ $t('ViewAs') }}:</label>
                                            <div v-if="isTypeInteger" class="input-group">
                                                <input name="sum" v-model="aggregateFunctionType" value="Sum" type="radio"
                                                    class="mr-2" />{{ $t('Sum') }}
                                                <input name="sum" v-model="aggregateFunctionType" value="Avg" type="radio"
                                                    class="mr-2 ml-3" />{{ $t('Average') }}
                                                <input name="sum" v-model="aggregateFunctionType" value="Min" type="radio"
                                                    class="mr-2 ml-3" />{{ $t('Min') }}
                                                <input name="sum" v-model="aggregateFunctionType" value="Max" type="radio"
                                                    class="mr-2 ml-3" />{{ $t('Max') }}
                                                <input name="sum" v-model="aggregateFunctionType" value="Count" type="radio"
                                                    class="mr-2 ml-3" />{{ $t('Count') }}
                                            </div>
                                            <div class="input-group" v-if="!isTypeInteger">
                                                <input name="sum" v-model="aggregateFunctionType" value="Count" checked
                                                    type="radio" class="mr-2 ml-3" />{{ $t('Count') }}
                                            </div>
                                        </div>
                                    </div>

                                </div>
                                <div class="col-sm-12 col-lg-6 p-3 bg-light border">
                                    <div class="heading-border">
                                        <h5 class="h5"><span>{{ $t('Preview') }}</span></h5>
                                    </div>
                                    <span class="text-center my-3 w-100 float-left">{{ reportTitle }} -
                                        <button href="#" class="btn btn-info" @click="ViewReportGraph">
                                            {{ $t('ViewReport') }}
                                        </button>
                                    </span>
                                    <div v-if="PreviewChart" class="col-sm-12 col-lg-12 px-0 py-4 float-left bg-white">
                                        <VueApexCharts :type="selectedChartType" :options="chartOptions"
                                            :series="chartseries">
                                        </VueApexCharts>
                                        <div :class="selectedChartType == 'pie' || selectedChartType == 'donut' ? 'text-left pl-4 ml-4' : 'text-center'"
                                            v-if="aggregateFunctionResult != 0">
                                            <a href="#">{{ resultMsg }} : {{ aggregateFunctionResult }}</a>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div class="col-12 p-0 mt-3 float-left">
                                <div class="row flex-row-reverse">
                                    <div class="col-lg-6 text-right">
                                        <button class="btn btn-success ancsaveuserticket" @click="ApplyGraph">
                                            <em class="fa fa-share pr-2"></em>{{ $t('Apply') }}</button>
                                    </div>
                                </div>
                            </div>
                        </Form> -->
                        <div class="col-12 p-0 mt-3 float-left">
                            <div class="row flex-row-reverse">
                                <div class="col-lg-6 text-right">
                                    <button class="btn btn-success ancsaveuserticket" @click="ApplyGraph">
                                        <em class="fa fa-share pr-2"></em>{{ $t('Apply') }}</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import GraphServices from "@/services/DataService";
import VueApexCharts from "vue-apexcharts";
export default {
    components: {
        VueApexCharts
    },
    props: {
        Columnlist: {
            type: Array
        },
        customreportparameters: {
            type: Object
        },
        GraphData: {
            type: Array
        },
        GraphJson: {
            type: Object
        },
        sortColummnarray: {
            type: Array
        },
    },
    data() {
        return {
            isLoading: false,
            reportTitle: '',
            chartTypes: null,
            // isTypeInteger: false,
            // Selected Chart Properties
            selectedChartTypeID: null,
            selectedChartXAxis: "",
            selectedChartYAxis: "",
            PreviewChart: false,
            aggregateFunctionType: "Count",
            aggregateFunctionResult: 0,
            //Chart Properties
            chartseries: [],
            chartOptions: {},
            colourList: ['#8892d0', '#da9c49', '#94c5a3', '#e67d21', '#787878', '#c59e71', '#38b8df', '#f55e1a', '#207a92', '#13599c'],
            // Properties for Binding the Chart Valus
            chartOptionXAxisData: [],
            chartSeriesData: [],
            selectedChartType: "",
            resultMsg: "",
            GroupingGraphData: [],
            BindGraph: false,
            isChartData: false,
            isGraphLoadedAlready: false,
            graphParams: {},
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('ReportTitle'),
                            name: "ReportTitle",
                            value: "",
                            placeholder: "Enter ReportTitle",
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: "",
                            disabled: true,
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('ChartTypes'),
                            name: "chartlist",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.onChangeDropdown
                            },
                            validationRules: "required",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('XAXIS'),
                            name: "XAXIS",
                            value: "",
                            placeholder: "Please Select X Axis",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.onChangeXYAxisDropdown
                            },
                            validationRules: "required",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('YAXIS'),
                            name: "YAXIS",
                            value: '',
                            placeholder: "",
                            //visibility:true,
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.onChangeXYAxisDropdown
                            },
                            validationRules: "required",
                        },
                        {
                            astype: "RadioField",
                            label: this.$t('ViewAs'),
                            name: "ViewAs",
                            value: "",
                            placeholder: "",
                            visibility: false,
                            config: {
                                options: [{
                                    value: "Sum",
                                    name: "Sum"
                                },
                                {
                                    value: "Avg",
                                    name: "Average"
                                },
                                {
                                    value: "Min",
                                    name: "Min"
                                }, {
                                    value: "Max",
                                    name: "Max"
                                }, {
                                    value: "Count",
                                    name: "Count"
                                }],
                                showAddIcon: false,
                                onChange: this.GetRadioValue
                            },
                            validationRules: "",
                        },
                        {
                            astype: "RadioField",
                            label: this.$t('ViewAs'),
                            name: "ViewAs",
                            value: "Count",
                            placeholder: "",
                            visibility: true,
                            config: {
                                options: [{
                                    value: "Count",
                                    name: "Count"
                                }],
                                showAddIcon: false
                            },
                            validationRules: "",
                        },
                    ]
                },
            ],
            // buttons: [
            //     {
            //         type: "submit",
            //         class: "btn btn-success save",
            //         text: "<i class='fa fa-save pr-2'></i>Apply"
            //     }
            // ]
        }
    },
    created: async function () {
        var vm = this;
        await vm.GetChartTypes();
        vm.GetColumnList();
        vm.FormSchema[0].Data[0].value = vm.customreportparameters.ReportName;
        if (vm.GraphData != null && vm.GraphData != undefined && vm.GraphData != '' && vm.GraphData.length > 0) {
            vm.selectedChartTypeID = vm.GraphData[0].chartTypeID;
            vm.aggregateFunctionType = vm.GraphData[0].AggregateFuncField;
            vm.selectedChartXAxis = vm.GraphData[0].XAxisField;
            vm.FormSchema[0].Data[2].value = vm.GraphData[0].XAxisField;
            vm.selectedChartYAxis = vm.GraphData[0].YAxisField;
            vm.FormSchema[0].Data[3].value = vm.GraphData[0].YAxisField;
            await vm.CheckFieldType(vm.selectedChartYAxis);
            await vm.ViewReportGraph();
            await vm.ApplyGraph();
        }
    },
    watch:{
        Columnlist(){
            var vm = this;
            vm.GetColumnList();
        }
    },
    methods: {
        //Close Graph Options Model
        CloseGraphOptions: function () {
            var vm = this;
            $("#CustomGraphOptions").modal("hide");
            $("body").removeClass("modal-open");
            $(".modal-backdrop").remove();
            vm.$refs.refGraphForm.validateForm.reset();
            if(vm.graphParams.selectedChartTypeID != null && vm.graphParams.selectedChartType != '' && vm.graphParams.selectedChartXAxis != '' && vm.graphParams.selectedChartYAxis != ''){
            vm.$parent.showChart = true;}
            // vm.ApplyGraph();
        },

        // Get all Chart types
        async GetChartTypes() {
            var vm = this;
            await GraphServices.GetChartTypes().then((response) => {
                vm.chartTypes = response.data;
            })
        },

        // Fill Values in Dropdowns
        GetColumnList() {
            var vm = this;
            vm.FormSchema[0].Data[2].config.options = [];
            vm.FormSchema[0].Data[3].config.options = [];
            vm.Columnlist.forEach(item => {
                vm.FormSchema[0].Data[2].config.options.push({
                    value: item.CustomFieldId,
                    name: item.DisplayName != '' ? vm.$t(item.DisplayName) : vm.$(item.FieldName)
                })
                vm.FormSchema[0].Data[3].config.options.push({
                    value: item.CustomFieldId,
                    name: item.DisplayName != '' ? vm.$t(item.DisplayName) : vm.$(item.FieldName)
                })
            })
        },

        // On change X and Y Axis
        onChangeXYAxisDropdown(x, data) {
            var vm = this;
            if (data.name == "XAXIS") {
                vm.selectedChartXAxis = data.value;
                vm.FormSchema[0].Data[3].config.options = [];
                vm.Columnlist.forEach(item => {
                    if (item.CustomFieldId != vm.selectedChartXAxis) {
                        vm.FormSchema[0].Data[3].config.options.push({
                            value: item.CustomFieldId,
                            name: item.DisplayName != '' ? vm.$t(item.DisplayName) : vm.$(item.FieldName)
                        })
                    }
                    if(vm.selectedChartXAxis === vm.selectedChartYAxis){
                        vm.selectedChartYAxis = '';
                        vm.FormSchema[0].Data[3].value = '';
                    }
                })
            }
            else {
                vm.selectedChartYAxis = data.value;
                vm.CheckFieldType(vm.selectedChartYAxis)
            }
        },

        // Get Radio Selected Value 
        GetRadioValue(x, data) {
            var vm = this;
            vm.aggregateFunctionType = x.target.dataset.value;
        },
        // Reset Graph properties when click on reset button 
        ResetGraph() {
            var vm = this;
            vm.selectedChartXAxis = "";
            vm.selectedChartYAxis = "";
            // vm.$refs.form.setFieldTouched('selectedChartXAxis', false)
            // vm.$refs.form.setFieldTouched('selectedChartYAxis', false)
            vm.chartseries = [];
            vm.chartOptions = {};
            vm.selectedChartTypeID = null;
            vm.selectedChartType = "";
            vm.aggregateFunctionResult = 0;
            vm.aggregateFunctionType = "Count";
            vm.resultMsg = "";
            vm.graphParams = {};
            vm.FormSchema[0].Data[2].value = '';
            vm.FormSchema[0].Data[3].value = '';
            vm.FormSchema[0].Data[4].value = 'Count';
            vm.FormSchema[0].Data[5].value = 'Count';
            vm.FormSchema[0].Data[4].visibility = false;
            vm.FormSchema[0].Data[5].visibility = true;
            vm.$refs.refGraphForm.validateForm.reset();
            vm.PreviewChart = false;
        },

        // View Graph when click on View Report
        async ViewReportGraph() {
            var vm = this;
            vm.resultMsg = vm.aggregateFunctionType;
            vm.PreviewChart = false;
            vm.BindGraph = true;
            var GraphDataValue = null;
                if (vm.aggregateFunctionType == null || vm.aggregateFunctionType == '' && vm.GraphData != null && vm.isChartData == false) {
                    if (vm.GraphData.length > 0) {
                        vm.resultMsg = vm.GraphData[0].AggregateFunction
                        vm.aggregateFunctionType = vm.resultMsg
                        vm.selectedChartYAxis = vm.GraphData[0].YAxisField;
                        vm.selectedChartXAxis = vm.GraphData[0].XAxisField;
                        vm.FormSchema[0].Data[4].value = vm.GraphData[0].AggregateFunction;
                    }
                }
                if (vm.selectedChartTypeID != null && vm.selectedChartXAxis != '' && vm.selectedChartYAxis != '') {
                    if (vm.aggregateFunctionType == "") {
                        vm.aggregateFunctionType = vm.FormSchema[0].Data[4].value;
                        vm.resultMsg = vm.aggregateFunctionType;
                    }
                    var findvaluefromcolumnlistxaxis = vm.Columnlist.find(x => x.FieldName === vm.selectedChartXAxis || x.CustomFieldId === parseInt(vm.selectedChartXAxis));
                    var findvaluefromcolumnlistyaxis = vm.Columnlist.find(x => x.FieldName === vm.selectedChartYAxis || x.CustomFieldId === parseInt(vm.selectedChartYAxis));
                    GraphDataValue = {
                        "XAxisField": findvaluefromcolumnlistxaxis.CustomFieldId != '' ? findvaluefromcolumnlistxaxis.CustomFieldId : findvaluefromcolumnlistxaxis.FieldName,
                        "YAxisField": findvaluefromcolumnlistyaxis.CustomFieldId != '' ? findvaluefromcolumnlistyaxis.CustomFieldId : findvaluefromcolumnlistyaxis.FieldName,
                        "AggregateFunctionType": vm.aggregateFunctionType
                    }
                    await vm.$parent.$parent.PreviewJson(vm.sortColummnarray, vm.BindGraph, GraphDataValue);
                    if (vm.GraphJson.MetaData[0].IsGrouping) {
                        vm.SetGroupingGraphData();
                    }
                    vm.selectedChartType = vm.chartTypes.find(x => x.id == vm.selectedChartTypeID).chart_Type.toLowerCase();
                    vm.SetGraphValues(vm.GroupingGraphData, vm.FilterColumnlist(vm.selectedChartXAxis).CustomFieldName, vm.FilterColumnlist(vm.selectedChartYAxis).CustomFieldName);
                    vm.CalculatingAggregateFuncResult(vm.aggregateFunctionType);
                    vm.DrawGraph();
                    vm.PreviewChart = true;
                    vm.isChartData = true
                    vm.isGraphLoadedAlready = true;
                }
                else if(vm.selectedChartTypeID == null && vm.selectedChartXAxis=='' && vm.selectedChartYAxis==''){
                    vm.ShowAlert(vm.$t("FieldsNameRequired", [vm.$t("ChartTypes") + ', ' + vm.$t("XAXIS") + ' and '+  vm.$t("YAXIS")]), 'failure', true, vm.$t('Alert'));
                    vm.isLoading = false;
                }
                else if(vm.selectedChartTypeID == null){
                    vm.ShowAlert(vm.$t("FieldNameRequired", [vm.$t("ChartTypes")]), 'failure', true, vm.$t('Alert'));
                    vm.isLoading = false;
                }
                else if(vm.selectedChartXAxis==''){
                    vm.ShowAlert(vm.$t("FieldNameRequired", [vm.$t("XAXIS")]), 'failure', true, vm.$t('Alert'));
                    vm.isLoading = false;
                }
                else if(vm.selectedChartYAxis==''){
                    vm.ShowAlert(vm.$t("FieldNameRequired", [vm.$t("YAXIS")]), 'failure', true, vm.$t('Alert'));
                    vm.isLoading = false;
                }
        },

        SetGroupingGraphData() {
            var vm = this;
            let ListingData = vm.GraphJson.ListingData;
            let setGraphArray = [];
            let XaxisField = vm.FilterColumnlist(vm.selectedChartXAxis).CustomFieldName;
            let yAxisField = vm.FilterColumnlist(vm.selectedChartYAxis).CustomFieldName;
            if (Object.keys(ListingData[0])[0] == vm.FilterColumnlist(vm.selectedChartXAxis).CustomFieldName && Object.keys(ListingData[0])[0] == vm.FilterColumnlist(vm.selectedChartYAxis).CustomFieldName && vm.GraphData.length > 0
                || XaxisField == undefined && yAxisField == undefined) {
                XaxisField = Object.keys(ListingData[0])[0];
                yAxisField = Object.keys(ListingData[0].data[0])[0];
                vm.aggregateFunctionType = vm.GraphData[0].AggregateFunction;
            }
            ListingData.forEach((item, mainindex) => {
                if (item[XaxisField] != undefined || XaxisField != undefined) {
                    if (vm.aggregateFunctionType == "Count" || vm.aggregateFunctionType == "Sum" || vm.aggregateFunctionType == "Avg"
                        || vm.aggregateFunctionType == "Min" || vm.aggregateFunctionType == "Max") {
                        var pushitem = item[XaxisField] != undefined ? item[XaxisField] : item.data[0][XaxisField] + ` (${item.totalRecord})`;
                        for (var i = 0; i <= parseInt(item.totalRecord); i++) {
                            if (i == 0) {
                                setGraphArray.push({
                                    [XaxisField]: pushitem,
                                    [yAxisField]: item.GData != null && item.GData != 0 && item.GData > item.totalRecord ? item.GData : item.totalRecord
                                });
                            }
                        }
                    }
                }
            })
            vm.GroupingGraphData = setGraphArray
        },
        // Apply  Graph Values when click on Apply adn bind the final values of graph and pass to Reportpreview 
        async ApplyGraph() {
            var vm = this;
            this.$refs.refGraphForm.validateForm.validate().then(async function (valid) {
                if (valid) {
                    if (!vm.isGraphLoadedAlready) {
                        await vm.ViewReportGraph();
                    }
                    vm.graphParams = {
                        "selectedChartType": vm.selectedChartType,
                        "selectedChartTypeID": vm.selectedChartTypeID,
                        "chartseries": vm.chartseries,
                        "chartoptions": vm.chartOptions,
                        "aggregateFunctionType": vm.aggregateFunctionType,
                        "aggregateFunctionResult": vm.aggregateFunctionResult,
                        "selectedChartXAxis": vm.selectedChartXAxis,
                        "selectedChartYAxis": vm.selectedChartYAxis
                    }
                    if (vm.graphParams.selectedChartTypeID != null && vm.graphParams.selectedChartType != '' && vm.graphParams.selectedChartXAxis != '' && vm.graphParams.selectedChartYAxis != '') {
                        vm.$parent.DrawGraph(vm.graphParams, vm.BindGraph)
                        vm.isGraphLoadedAlready = false
                        vm.CloseGraphOptions();
                    }
                }
            });
        },

        // Draw Graph
        DrawGraph: function () {
            var vm = this;
            var isHorizontal = false;
            if (vm.selectedChartType == "bar" || vm.selectedChartType == "line" || vm.selectedChartType == 'horizontal_bar') {
                if (vm.selectedChartType == 'horizontal_bar') {
                    vm.selectedChartType = "bar";
                    isHorizontal = true;
                }
                vm.chartseries = [{
                    name: vm.$t(vm.FilterColumnlist(vm.selectedChartYAxis).CustomFieldName),
                    data: vm.chartSeriesData
                }]
                vm.chartOptions = {
                    chart: {
                        toolbar: {
                            export: {
                                svg: {
                                    filename: vm.FormSchema[0].Data[0].value,
                                },
                                png: {
                                    filename: vm.FormSchema[0].Data[0].value,
                                },
                                csv: {
                                    headerCategory: vm.$t(vm.FilterColumnlist(vm.selectedChartXAxis).CustomFieldName),
                                    filename: vm.FormSchema[0].Data[0].value,
                                }
                            }
                        },
                    },
                    tooltip:{
                        enabled:true,
                        x:{
                            formatter:(val) => val
                        }
                    },
                    xaxis: {
                        categories: vm.chartOptionXAxisData,
                        title: {
                            text: isHorizontal ? vm.$t(vm.FilterColumnlist(vm.selectedChartYAxis).CustomFieldName) : vm.$t(vm.FilterColumnlist(vm.selectedChartXAxis).CustomFieldName),
                        },
                        labels: {
                            formatter: function (val) {
                                return isHorizontal ? val : val != undefined ? vm.TruncateStr(val,6) : val;
                            }
                        },
                    },
                    yaxis: {
                        title: {
                            text: isHorizontal ? vm.$t(vm.FilterColumnlist(vm.selectedChartXAxis).CustomFieldName) : vm.$t(vm.FilterColumnlist(vm.selectedChartYAxis).CustomFieldName),
                        },
                        labels: {
                            formatter: function (val) {
                                return val.toLocaleString("en-US");
                            }
                        }
                    },
                    plotOptions: isHorizontal ? { bar: { borderRadius: 4, horizontal: true, } } : {},
                    dataLabels: {
                        enabled: true,
                    },

                }
            }
            if (vm.selectedChartType == "pie" || vm.selectedChartType == "donut") {
                vm.chartseries = vm.chartSeriesData;
                vm.chartOptions = {
                    labels: vm.chartOptionXAxisData,
                    colors: vm.colourList,
                }
            }
        },
        TruncateStr: function (str, len) {
            var convertstring = '' + str;
            if (convertstring != null)
                if (convertstring.length <= len) return str;
                else return convertstring.substring(0, len) + "...";
        },

        // Calculating  Aggregate Function Result
        CalculatingAggregateFuncResult: function (functionType) {
            var vm = this;
            var tempval = 0;
            if (functionType == "Sum") {
                var sum = 0;
                vm.chartSeriesData.forEach(item => {
                    sum += parseInt(item);
                });
                vm.aggregateFunctionResult = sum.toFixed(2);
                vm.aggregateFunctionResult = parseFloat(vm.aggregateFunctionResult).toLocaleString('en-US');
            }
            if (functionType == "Count") {
                var aggregatecount = 0;
                aggregatecount = vm.chartSeriesData.length;
                vm.aggregateFunctionResult = aggregatecount;
            }
            if (functionType == "Avg") {
                var total = 0;
                var count = 0;

                vm.chartSeriesData.forEach(function (item, index) {
                    total += item;
                    count++;
                });
                tempval = total / count;
                vm.aggregateFunctionResult = tempval.toFixed(2);
                vm.aggregateFunctionResult = parseFloat(vm.aggregateFunctionResult).toLocaleString('en-US');
            }
            if (functionType == "Min") {
                tempval = Math.min.apply(Math, vm.chartSeriesData.filter(Number));
                vm.aggregateFunctionResult = tempval.toFixed(2);
                vm.aggregateFunctionResult = parseFloat(vm.aggregateFunctionResult).toLocaleString('en-US');
            }
            if (functionType == "Max") {
                tempval = Math.max(...vm.chartSeriesData)
                vm.aggregateFunctionResult = tempval.toFixed(2);
                vm.aggregateFunctionResult = parseFloat(vm.aggregateFunctionResult).toLocaleString('en-US');
            }
        },

        // Check field type for View AS checkbox 
        CheckFieldType: function (CustomFieldId) {
            var vm = this;
            if (vm.selectedChartYAxis == vm.selectedChartXAxis) {
                vm.selectedChartYAxis = '';
                vm.ShowAlert("X and Y axis values are not same", "warning")
                return false;
            }
            vm.aggregateFunctionResult = 0;
            let ColumnFieldType = vm.FilterColumnlist(CustomFieldId).ColumnFieldType;
            if (ColumnFieldType == "decimal" || ColumnFieldType == "int") {
                vm.FormSchema[0].Data[4].visibility = true;
                vm.FormSchema[0].Data[5].visibility = false;
                vm.FormSchema[0].Data[4].value = 'Count'
                vm.aggregateFunctionType = ''
            }
            else {
                vm.FormSchema[0].Data[4].visibility = false;
                vm.FormSchema[0].Data[5].visibility = true;
                vm.FormSchema[0].Data[5].value = 'Count'
                vm.aggregateFunctionType = 'Count'
            }
        },
        CheckFieldTypeX: function (CustomFieldId) {
            var vm = this;
            //vm.aggregateFunctionType = ''
            if (vm.selectedChartYAxis == vm.selectedChartXAxis) {
                vm.selectedChartXAxis = '';
                vm.ShowAlert("X and Y axis values are not same", "warning")
                return false;
            }
        },
        // Filter column List baesd on CustomFieldId
        FilterColumnlist(CustomFieldId) {
            var vm = this;
            let CustomFieldName, ColumnFieldType;
            for (let [index] of vm.Columnlist.entries()) {
                if (vm.Columnlist[index].CustomFieldId == CustomFieldId || vm.Columnlist[index].FieldName == CustomFieldId) {
                    if (vm.Columnlist[index].DisplayName != null && vm.Columnlist[index].DisplayName != '') {
                        CustomFieldName = vm.Columnlist[index].DisplayName;
                    }
                    else {
                        CustomFieldName = vm.Columnlist[index].FieldName;
                    }
                    ColumnFieldType = vm.Columnlist[index].ColumnFieldDataCode;
                }
            }
            return { 'CustomFieldName': CustomFieldName, 'ColumnFieldType': ColumnFieldType }
        },

        // Set the Chart Xaxis and Yaxis
        SetGraphValues(data, XFieldName, YFieldName) {
            var vm = this;
            vm.chartOptionXAxisData = [];
            vm.chartSeriesData = [];
            data.forEach(items => {
                if (XFieldName in items) {
                    if (items[XFieldName] != null && items[XFieldName] != undefined) {
                        vm.chartOptionXAxisData.push(items[XFieldName])
                        //set graph Values if XField  is Not Null  
                        vm.BindXYFields(items, XFieldName, YFieldName);
                    }
                    else {
                        //set graph Values if XField  is  Null
                        if (items[YFieldName] != null && items[YFieldName] != undefined) {
                            if (typeof XFieldName == "string") {
                                vm.chartOptionXAxisData = [...vm.chartOptionXAxisData, "-"]
                            }
                            else {
                                vm.chartOptionXAxisData = [...vm.chartOptionXAxisData, 0]
                            }
                            vm.BindXYFields(items, XFieldName, YFieldName);
                        }

                    }
                }
                else {
                    vm.chartOptionXAxisData.push(" ")
                    vm.BindXYFields(items, XFieldName, YFieldName);
                }
            });
        },
        //set graph Values if XField  is Not Null  
        BindXYFields: function (items, XFieldName, YFieldName) {
            var vm = this;
            if (YFieldName in items) {
                if (items[YFieldName] != null && items[YFieldName] != undefined) {
                    if (typeof items[YFieldName] == "string") {
                        var replaceXCurrencySymbol = items[YFieldName].replace(/[$€£]/g, '')
                        vm.chartSeriesData.push(parseInt(replaceXCurrencySymbol))
                    }
                    else {
                        vm.chartSeriesData.push(items[YFieldName])
                    }
                }
                else {
                    vm.chartSeriesData = [...vm.chartSeriesData, 0]
                }
            }
        },

        //Genrate Random Color Code
        GenrateRandomColorCode() {
            const randomColor = Math.floor(Math.random() * 16777215).toString(16);
            return "#" + randomColor;
        },

        // Genrate List of random colors
        GenrateRandomColorList() {
            var vm = this;
            while (vm.colourList.length < vm.chartSeriesData.length) {
                let color;
                color = vm.GenrateRandomColorCode();
                let IsExist = vm.colourList.find(x => x == color)
                if (!IsExist) { vm.colourList.push(color); }
            }
        },
        onSubmit: async function (formfields, val) {
            var vm = this;
            await vm.ApplyGraph();
            // vm.SaveDynamicreport(ReportID)            
        },
    }
}

</script>