<script>
import ExpressionDataService from "@/services/DataService";
export default {
    props: {
        customreportparameters: {
            type: Object
        },
        ExpDataArray: {
            type: Array
        },
        editexpressionvalue: {
            type: Boolean
        },
        ExpFieldDataArray: {
            type: Array
        },
        Columnlist: {
            type: Array
        },
        AlreadyExistExpression: {
            typeL: Object
        }
    },
    data() {
        return {
            JsonColumns: [],
            modulelisting: [],
            FunctionsName: [],
            Functionskey: [],
            ResponseColumnData: [],
            ResponseFunctionData: [],
            watchvalue: false,
            FieldsValue: '',
            FunctionValue: '',
            AddBracketValue: '',
            SubtractBracketValue: '',
            MultplyBracketValue: '',
            DivideBracketValue: '',
            OpenBracketExpValue: '',
            CloseBracketExpValue: '',
            InputValueExpression: '',
            FieldData: [],
            fieldatavalue: [],
            reqFrom: 'Reporting',
            isLoading: false,
            verifiedvalue: false,
            modelfieldname: '',
            substringvalue: '',
            verifiedvaluedata: null,
            verifyErrorMsg: '',
            InputValueArray: [],
            InputValueExpressionByID: '',
            FunctionValueArray: 'No_Value',
            selecteduserinput: null,
            selecteduserinputbyid: null,
            ExpressionIDvalue: '',
            updateexpresion: true,
            fieldadded: false,
            FieldTableNameValue: '',
            GetCommaSeparatedValue: '',
            countbyfield: 0,
            modelconditionexpression: '',
            conditionexpression: false,
            Functionkey: null,
            ShowDescriptionForFunction: null,
            checkbycondition: 0,
            searchColumnfilter: [],
            MainSearchmodel: '',
            SearchNoRecord: false,
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('fieldName'),
                            name: "fieldName",
                            value: "",
                            placeholder: "Enter Field Name",
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            // validationRules: "required",
                            validationRules: { "required": true, max: 100 },

                        },
                        {
                            astype: "SlotField",
                            label: '',
                            name: "CalculatedUIList",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.onChangeDropdown
                            },
                            validationRules: "",
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success mr-2 my-sm-0 disabled",
                    text: "<i class='fa fa-save pr-2'></i>Submit",
                },
                {
                    type: "button",
                    class: "btn btn-primary delete-btn mr-2 my-sm-0",
                    text: "<i class='fa fa-times pr-2'></i>Cancel",
                    onClick: this.CancelExpressionUI
                }
            ]
        }
    },
    created() {
        var vm = this;
        vm.GetColumnList(vm.customreportparameters);
        if (vm.editexpressionvalue == true && vm.ExpDataArray.length > 0) {
            vm.ExpresssionDataValue(vm.ExpDataArray, vm.ExpFieldDataArray);
        }
        vm.GetFunctionList();
    },
    mounted: function () {
        $('#showcalculatedfield').modal({
            backdrop: 'static',
            keyboard: false
        })
    },
    watch: {
        watchvalue(newval) {
            var vm = this;
            if (newval) {
                vm.GetColumnList(vm.customreportparameters);
                vm.GetFunctionList();
                vm.watchvalue = false;
                $("#Submitbtn").addClass('disabled');
                if (vm.editexpressionvalue == true && vm.ExpDataArray.length > 0 && vm.updateexpresion == true) {
                    vm.ExpresssionDataValue(vm.ExpDataArray, vm.ExpFieldDataArray);
                }
            }
        }
    },
    methods: {
        close() {
            $("#showcalculatedfield").modal("hide"); $("body").removeClass("modal-open");
            $(".modal-backdrop").remove();
            this.$emit('close');
            // this.modelfieldname = '';
            this.FormSchema[0].Data[0].value = '';
            // this.FormSchema[0].Data[0].validationRules = '';
            this.ExpressionIDvalue = '';
            this.ResetExpression();
            //this.$refs.form.setFieldTouched('modelfieldname', false);
        },
        GetColumnList(customreportparameters) {
            var vm = this;
            if (customreportparameters.Modulename != undefined && customreportparameters.Submodulecode != undefined && customreportparameters.ReportName != null) {
                var params = `moduleName=${customreportparameters.Modulename}&subModuleCode=${customreportparameters.Submodulecode}&viewId&reqFrom=${vm.reqFrom}`;
                ExpressionDataService.GetListFields(params).then(response => {
                    vm.JsonColumns = response.data.AVAILABLE_FIELDS[0];
                    vm.JsonColumns.forEach((item) => {
                        vm.modulelisting.push(item.CustomFields);
                    })
                }).catch(err => {
                    console.log(err);
                })
            }
        },
        GetFunctionList() {
            var vm = this;
            ExpressionDataService.GetFunctionList().then(res => {
                vm.FunctionsName = res.data.AVAILABLE_FIELDS[0];
                vm.FunctionsName.forEach((name) => {
                    vm.Functionskey.push(name.Functions);
                })
            }).catch(err => {
                console.log(err)
            })
        },
        Addfields(element, event, index) {
            var vm = this;
            if (vm.modelconditionexpression == '') { vm.FieldData.push({ CustomFieldId: element.CustomFieldId, FieldName: element.FieldName }); }

            if (vm.substringvalue == '') {
                var cursorpos = vm.GetCursorPoint();
                var getdata = vm.SetValueOnCursorPosition(cursorpos);
                if (getdata.length > 1) {
                    var checkindex = vm.InputValueArray.findIndex(x => x.FieldValueData === getdata[0].FieldValueData);
                    if (checkindex > -1) {
                        vm.fieldatavalue.splice(checkindex, 0, {
                            FieldValueData: vm.$t((element.DisplayName != null && element.DisplayName != "") ? element.DisplayName : element.FieldName),
                            MatchIndex: vm.$t((element.DisplayName != null && element.DisplayName != "") ? element.DisplayName : element.FieldName).length, SameName: 'Field_Added' + cursorpos
                        })
                        vm.FieldsValue = vm.fieldatavalue.map(x => x.FieldValueData).join('')
                        vm.fieldadded = true;
                    }
                } else {
                    vm.fieldatavalue.push({
                        FieldValueData: vm.$t((element.DisplayName != null && element.DisplayName != "") ? element.DisplayName : element.FieldName),
                        MatchIndex: vm.$t((element.DisplayName != null && element.DisplayName != "") ? element.DisplayName : element.FieldName).length, SameName: 'Field_Added' + cursorpos
                    });
                    vm.FieldsValue = vm.fieldatavalue.map(x => x.FieldValueData).join('');
                    vm.fieldadded = false;
                }
                $("#Submitbtn").addClass('disabled');
            }
            else {
                if (typeof vm.fieldatavalue == 'object') {
                    var convertarr = String(vm.fieldatavalue.map(x => x.FieldValueData));
                    vm.fieldatavalue = convertarr
                }
                if (vm.GetCommaSeparatedValue == "CommaSeparatedValue" && vm.countbyfield == 0 && !vm.conditionexpression) {
                    var firstindex = vm.fieldatavalue.indexOf(",") + 1;
                    var secondindex = vm.fieldatavalue.indexOf(")")
                }
                else if (vm.GetCommaSeparatedValue == "CommaSeparatedValue" && vm.countbyfield > 0 && !vm.conditionexpression) {
                    var firstindex = vm.fieldatavalue.indexOf(vm.fieldatavalue.split(",")[vm.countbyfield]);
                    var secondindex = vm.fieldatavalue.indexOf(")")
                }
                else {
                    var firstindex = vm.fieldatavalue.indexOf("(") + 1;
                    var secondindex = vm.fieldatavalue.indexOf(")")
                }
                vm.substringvalue = vm.fieldatavalue.substring(firstindex, secondindex);
                vm.FunctionValueArray = vm.substringvalue;
                if (vm.substringvalue.includes(',')) {
                    var substringarr = vm.substringvalue.split(',')
                    vm.substringvalue = substringarr;
                    if (!vm.conditionexpression && vm.checkbycondition == 0) {
                        vm.FieldsValue = vm.fieldatavalue.replace(vm.substringvalue[0],
                            vm.$t((element.DisplayName != null && element.DisplayName != "") ? element.DisplayName : element.FieldName))
                    }
                    else {
                        vm.FieldsValue = vm.fieldatavalue.replace(vm.substringvalue[0], element)
                    }
                    // vm.fieldatavalue = vm.FieldsValue;
                    vm.fieldatavalue = [];
                    vm.fieldatavalue.push({
                        FieldValueData: vm.FieldsValue,
                        MatchIndex: vm.FieldsValue.length, SameName: 'Field_Added' + vm.FieldsValue.length
                    });
                    $("#Submitbtn").addClass('disabled');
                } else {
                    if (!vm.conditionexpression && vm.checkbycondition == 0) {
                        vm.FieldsValue = vm.fieldatavalue.replace(vm.substringvalue,
                            vm.$t((element.DisplayName != null && element.DisplayName != "") ? element.DisplayName : element.FieldName))
                    }
                    else {
                        vm.FieldsValue = vm.fieldatavalue.replace(vm.substringvalue, element);
                    }
                    // vm.fieldatavalue = vm.FieldsValue;
                    vm.fieldatavalue = [];
                    vm.fieldatavalue.push({
                        FieldValueData: vm.FieldsValue,
                        MatchIndex: vm.FieldsValue.length, SameName: 'Field_Added' + vm.FieldsValue.length
                    });
                    $("#Submitbtn").addClass('disabled');

                }
            }
            if (!vm.conditionexpression && vm.checkbycondition == 0) {
                if (element.IsSystemGenerated) {
                    vm.FieldTableNameValue = element.TableName + '.' + element.FieldName;
                }
                else {
                    vm.FieldTableNameValue = element.FieldName;
                }
                // $(event.target).closest(".parent-element").remove();
            } else { vm.FieldTableNameValue = element; }
            vm.updatingvalue();
            vm.updatevaluebyid(element, checkindex, getdata, cursorpos);
        },
        AddFunctionsfields(element, event, index) {
            var vm = this;
            if (vm.InputValueExpression == null || vm.InputValueExpression == undefined || vm.InputValueExpression == "") {
                if (typeof vm.fieldatavalue == 'string') {
                    var substringarr = vm.fieldatavalue.split(',')
                    vm.fieldatavalue = substringarr;
                }
                vm.fieldatavalue.push({ FieldValueData: element.Value, MatchIndex: element.Value.length, SameName: 'Function_Added' });
                vm.FieldsValue = vm.fieldatavalue.map(x => x.FieldValueData).join('');
                vm.InputValueArray.push({ FieldValueData: element.Value, MatchIndex: element.Value.length, SameName: 'Function_Added' });
                vm.Functionkey = element.Keys;
                vm.DescriptionForFunction();
                if (element.Keys == "Case") {
                    vm.substringvalue = ''
                } else { vm.substringvalue = "Function_Added"; }
                $(event.target).closest(".parent-element").remove();
                vm.updatingvalue();
                vm.updatevaluebyid();
            }
            else {
                vm.ShowAlert("You Cannot Select More Than One Function.", "Failure");
            }

        },
        Addopenbracketsign() {
            var vm = this;
            if (typeof vm.fieldatavalue == 'string') {
                var substringarr = vm.fieldatavalue.split(',')
                vm.fieldatavalue = substringarr;
            }
            var openbracketexpsign = '('
            var openbracket = openbracketexpsign.split('');
            var cursorpos = vm.GetCursorPoint();
            var getdata = vm.SetValueOnCursorPosition(cursorpos);
            if (getdata.length > 1) {
                var checkindex = vm.InputValueArray.findIndex(x => x.FieldValueData === getdata[0].FieldValueData);
                if (checkindex > -1) {
                    vm.fieldatavalue.splice(checkindex, 0, {
                        FieldValueData: openbracket, MatchIndex: openbracket.length, SameName: 'Open_BracketAdded' + cursorpos
                    })
                    vm.FieldsValue = vm.fieldatavalue.map(x => x.FieldValueData).join('');
                    vm.InputValueArray.splice(checkindex, 0, {
                        FieldValueData: openbracket, MatchIndex: openbracket.length, SameName: 'Open_BracketAdded' + cursorpos
                    })
                }
            } else {
                vm.fieldatavalue.push({ FieldValueData: openbracket, MatchIndex: openbracket.length, SameName: 'Open_BracketAdded' + cursorpos })
                vm.FieldsValue = vm.fieldatavalue.map(x => x.FieldValueData).join('');
                vm.InputValueArray.push({ FieldValueData: openbracket, MatchIndex: openbracket.length, SameName: 'Open_BracketAdded' + cursorpos })
            }
            vm.substringvalue = '';
            vm.FunctionValueArray = 'No_Value';
            vm.updatingvalue();
            vm.updatevaluebyid();
        },
        Addclosebracketsign() {
            var vm = this;
            if (typeof vm.fieldatavalue == 'string') {
                var substringarr = vm.fieldatavalue.split(',')
                vm.fieldatavalue = substringarr;
            }
            var closebracketexpsign = ')'
            var closebracket = closebracketexpsign.split('');
            var cursorpos = vm.GetCursorPoint();
            var getdata = vm.SetValueOnCursorPosition(cursorpos);
            if (getdata.length > 1) {
                var checkindex = vm.InputValueArray.findIndex(x => x.FieldValueData === getdata[0].FieldValueData);
                if (checkindex > -1) {
                    vm.fieldatavalue.splice(checkindex, 0, {
                        FieldValueData: closebracket, MatchIndex: closebracket.length, SameName: 'Close_BracketAdded' + cursorpos
                    })
                    vm.FieldsValue = vm.fieldatavalue.map(x => x.FieldValueData).join('');
                    vm.InputValueArray.splice(checkindex, 0, {
                        FieldValueData: closebracket, MatchIndex: closebracket.length, SameName: 'Close_BracketAdded' + cursorpos
                    })
                }
            } else {
                vm.fieldatavalue.push({ FieldValueData: closebracket, MatchIndex: closebracket.length, SameName: 'Close_BracketAdded' + cursorpos })
                vm.FieldsValue = vm.fieldatavalue.map(x => x.FieldValueData).join('');
                vm.InputValueArray.push({ FieldValueData: closebracket, MatchIndex: closebracket.length, SameName: 'Close_BracketAdded' + cursorpos })
            }
            vm.updatingvalue();
            vm.updatevaluebyid();
        },
        Addplussign() {
            var vm = this;
            if (typeof vm.fieldatavalue == 'string') {
                var substringarr = vm.fieldatavalue.split(',')
                vm.fieldatavalue = substringarr;
            }
            var addbracketexpsign = '+';
            var Addsign = addbracketexpsign.split('');
            var cursorpos = vm.GetCursorPoint();
            var getdata = vm.SetValueOnCursorPosition(cursorpos);
            if (getdata.length > 1) {
                var checkindex = vm.InputValueArray.findIndex(x => x.FieldValueData === getdata[0].FieldValueData);
                if (checkindex > -1) {
                    vm.fieldatavalue.splice(checkindex, 0, {
                        FieldValueData: Addsign, MatchIndex: Addsign.length, SameName: 'AddedPlus' + cursorpos
                    })
                    vm.FieldsValue = vm.fieldatavalue.map(x => x.FieldValueData).join('');
                    vm.InputValueArray.splice(checkindex, 0, {
                        FieldValueData: Addsign, MatchIndex: Addsign.length, SameName: 'AddedPlus' + cursorpos
                    })
                }
            } else {
                vm.fieldatavalue.push({ FieldValueData: Addsign, MatchIndex: Addsign.length, SameName: 'AddedPlus' + cursorpos })
                vm.FieldsValue = vm.fieldatavalue.map(x => x.FieldValueData).join('');
                vm.InputValueArray.push({ FieldValueData: Addsign, MatchIndex: Addsign.length, SameName: 'AddedPlus' + cursorpos })
            }
            vm.FunctionValueArray = 'No_Value';
            vm.updatingvalue();
            vm.updatevaluebyid();
        },
        Addsubtractsign() {
            var vm = this;
            if (typeof vm.fieldatavalue == 'string') {
                var substringarr = vm.fieldatavalue.split(',')
                vm.fieldatavalue = substringarr;
            }
            var subtractbracketexpsign = '-';
            var subtractsign = subtractbracketexpsign.split('');
            var cursorpos = vm.GetCursorPoint();
            var getdata = vm.SetValueOnCursorPosition(cursorpos);
            if (getdata.length > 1) {
                var checkindex = vm.InputValueArray.findIndex(x => x.FieldValueData === getdata[0].FieldValueData);
                if (checkindex > -1) {
                    vm.fieldatavalue.splice(checkindex, 0, {
                        FieldValueData: subtractsign, MatchIndex: subtractsign.length, SameName: 'AddedSubtract' + cursorpos
                    })
                    vm.FieldsValue = vm.fieldatavalue.map(x => x.FieldValueData).join('');
                    vm.InputValueArray.splice(checkindex, 0, {
                        FieldValueData: subtractsign, MatchIndex: subtractsign.length, SameName: 'AddedSubtract' + cursorpos
                    })
                }
            } else {
                vm.fieldatavalue.push({ FieldValueData: subtractsign, MatchIndex: subtractsign.length, SameName: 'AddedSubtract' + cursorpos })
                vm.FieldsValue = vm.fieldatavalue.map(x => x.FieldValueData).join('');
                vm.InputValueArray.push({ FieldValueData: subtractsign, MatchIndex: subtractsign.length, SameName: 'AddedSubtract' + cursorpos })
            }
            vm.FunctionValueArray = 'No_Value';
            vm.updatingvalue();
            vm.updatevaluebyid();
        },
        Addmultiplysign() {
            var vm = this;
            if (typeof vm.fieldatavalue == 'string') {
                var substringarr = vm.fieldatavalue.split(',')
                vm.fieldatavalue = substringarr;
            }
            var multiplybracketexpsign = '*';
            var multiplysign = multiplybracketexpsign.split('');
            var cursorpos = vm.GetCursorPoint();
            var getdata = vm.SetValueOnCursorPosition(cursorpos);
            if (getdata.length > 1) {
                var checkindex = vm.InputValueArray.findIndex(x => x.FieldValueData === getdata[0].FieldValueData);
                if (checkindex > -1) {
                    vm.fieldatavalue.splice(checkindex, 0, {
                        FieldValueData: multiplysign, MatchIndex: multiplysign.length, SameName: 'AddedMultiply' + cursorpos
                    })
                    vm.FieldsValue = vm.fieldatavalue.map(x => x.FieldValueData).join('');
                    vm.InputValueArray.splice(checkindex, 0, {
                        FieldValueData: multiplysign, MatchIndex: multiplysign.length, SameName: 'AddedMultiply' + cursorpos
                    })
                }
            } else {
                vm.fieldatavalue.push({ FieldValueData: multiplysign, MatchIndex: multiplysign.length, SameName: 'AddedMultiply' + cursorpos })
                vm.FieldsValue = vm.fieldatavalue.map(x => x.FieldValueData).join('');
                vm.InputValueArray.push({ FieldValueData: multiplysign, MatchIndex: multiplysign.length, SameName: 'AddedMultiply' + cursorpos })
            }
            vm.FunctionValueArray = 'No_Value';
            vm.updatingvalue();
            vm.updatevaluebyid();
        },
        Adddividesign() {
            var vm = this;
            if (typeof vm.fieldatavalue == 'string') {
                var substringarr = vm.fieldatavalue.split(',')
                vm.fieldatavalue = substringarr;
            }
            var dividebracketexpsign = '/';
            var dividesign = dividebracketexpsign.split('');
            var cursorpos = vm.GetCursorPoint();
            var getdata = vm.SetValueOnCursorPosition(cursorpos);
            if (getdata.length > 1) {
                var checkindex = vm.InputValueArray.findIndex(x => x.FieldValueData === getdata[0].FieldValueData);
                if (checkindex > -1) {
                    vm.fieldatavalue.splice(checkindex, 0, {
                        FieldValueData: dividesign, MatchIndex: dividesign.length, SameName: 'AddedDivide' + cursorpos
                    })
                    vm.FieldsValue = vm.fieldatavalue.map(x => x.FieldValueData).join('');
                    vm.InputValueArray.splice(checkindex, 0, {
                        FieldValueData: dividesign, MatchIndex: dividesign.length, SameName: 'AddedDivide' + cursorpos
                    })
                }
            } else {
                vm.fieldatavalue.push({ FieldValueData: dividesign, MatchIndex: dividesign.length, SameName: 'AddedDivide' + cursorpos })
                vm.FieldsValue = vm.fieldatavalue.map(x => x.FieldValueData).join('');
                vm.InputValueArray.push({ FieldValueData: dividesign, MatchIndex: dividesign.length, SameName: 'AddedDivide' + cursorpos })
            }
            vm.FunctionValueArray = 'No_Value';
            vm.updatingvalue();
            vm.updatevaluebyid();
        },
        AddSpace() {
            var vm = this;
            if (typeof vm.fieldatavalue == 'string') {
                var substringarr = vm.fieldatavalue.split(',')
                vm.fieldatavalue = substringarr;
            }
            var spaceaddingexp = ' ';
            var addedspace = spaceaddingexp.split('');
            var addedspacefordb = spaceaddingexp.replaceAll(' ', "+' '+");
            var cursorpos = vm.GetCursorPoint();
            var getdata = vm.SetValueOnCursorPosition(cursorpos);
            if (getdata.length > 0) {
                var checkindex = vm.InputValueArray.findIndex(x => x.FieldValueData === getdata[0].FieldValueData);
                if (checkindex > -1) {
                    vm.fieldatavalue.splice(checkindex, 0, {
                        FieldValueData: addedspace, MatchIndex: addedspace.length, SameName: 'addedspace' + cursorpos
                    })
                    vm.FieldsValue = vm.fieldatavalue.map(x => x.FieldValueData).join('');
                    vm.InputValueArray.splice(checkindex, 0, {
                        FieldValueData: addedspacefordb, MatchIndex: addedspace.length, SameName: 'addedspace' + cursorpos
                    })
                    vm.InputValueExpressionByID = vm.InputValueArray.map(x => x.FieldValueData).join('');
                }
            } else {
                vm.fieldatavalue.push({ FieldValueData: addedspace, MatchIndex: addedspace.length, SameName: 'addedspace' + cursorpos })
                vm.FieldsValue = vm.fieldatavalue.map(x => x.FieldValueData).join('');
                vm.InputValueArray.push({ FieldValueData: addedspacefordb, MatchIndex: addedspace.length, SameName: 'addedspace' + cursorpos })
                vm.InputValueExpressionByID = vm.InputValueArray.map(x => x.FieldValueData).join('');
            }
            vm.FunctionValueArray = 'No_Value';
            vm.updatingvalue();
        },
        ResetExpression() {
            var vm = this;
            vm.FieldsValue = '';
            vm.FunctionValue = '';
            vm.AddBracketValue = '';
            vm.SubtractBracketValue = '';
            vm.MultplyBracketValue = '';
            vm.DivideBracketValue = '';
            vm.OpenBracketExpValue = '';
            vm.CloseBracketExpValue = '';
            vm.modulelisting.splice(0);
            vm.Functionskey.splice(0);
            vm.InputValueExpression = '';
            vm.InputValueExpressionByID = '';
            vm.InputValueArray = [];
            vm.FieldData = [];
            vm.fieldatavalue = [];
            vm.watchvalue = true;
            vm.updateexpresion = false;
            vm.verifiedvaluedata = null;
            vm.FunctionValueArray = 'No_Value';
            vm.GetCommaSeparatedValue = '';
            vm.countbyfield = 0;
            vm.modelconditionexpression = '';
            vm.Functionkey = null;
            vm.ShowDescriptionForFunction = null;
            vm.conditionexpression = false;
            vm.checkbycondition = 0;
            vm.substringvalue = '';
        },
        updatevaluebyid(elem, checkindex, getdata, cursorpos) {
            var vm = this;
            if (vm.FunctionValueArray == vm.substringvalue) {
                if (typeof vm.InputValueArray == 'object') {
                    var convertarrid = String(vm.InputValueArray.map(x => x.FieldValueData));
                    vm.InputValueArray = convertarrid
                }
                if (vm.GetCommaSeparatedValue == "CommaSeparatedValue" && vm.countbyfield == 0 && !vm.conditionexpression) {
                    var firstindex = vm.InputValueArray.indexOf(",") + 1;
                    var secondindex = vm.InputValueArray.indexOf(")")
                    vm.countbyfield = vm.countbyfield + 1 + 1;
                }
                else if (vm.GetCommaSeparatedValue == "CommaSeparatedValue" && vm.countbyfield > 0 && !vm.conditionexpression) {
                    var firstindex = vm.InputValueArray.indexOf(vm.InputValueArray.split(",")[vm.countbyfield]);
                    var secondindex = vm.InputValueArray.indexOf(")")
                    vm.countbyfield++;
                }
                else {
                    var firstindex = vm.InputValueArray.indexOf("(") + 1;
                    var secondindex = vm.InputValueArray.indexOf(")")
                }
                vm.substringvalue = vm.InputValueArray.substring(firstindex, secondindex);
                if (vm.substringvalue.includes(',')) {
                    var substringarrid = vm.substringvalue.split(',')
                    vm.substringvalue = substringarrid;
                    vm.InputValueExpressionByID = vm.InputValueArray.replace(vm.substringvalue[0], vm.FieldTableNameValue)
                    vm.InputValueArray = [];
                    // vm.InputValueArray = vm.InputValueExpressionByID;
                    vm.InputValueArray.push({
                        FieldValueData: vm.InputValueExpressionByID,
                        MatchIndex: vm.InputValueExpressionByID.length,
                        SameName: 'Field_Added' + vm.InputValueExpressionByID.length,
                        CustomFieldId: elem.CustomFieldId
                    });
                    vm.GetCommaSeparatedValue = "CommaSeparatedValue"
                    $("#Submitbtn").addClass('disabled');
                } else {
                    vm.InputValueExpressionByID = vm.InputValueArray.replace(vm.substringvalue, vm.FieldTableNameValue)
                    // vm.InputValueArray = vm.InputValueExpressionByID;
                    vm.InputValueArray = [];
                    vm.InputValueArray.push({
                        FieldValueData: vm.InputValueExpressionByID,
                        MatchIndex: vm.InputValueExpressionByID.length,
                        SameName: 'Field_Added' + vm.InputValueExpressionByID.length,
                        CustomFieldId: elem.CustomFieldId
                    });
                    vm.substringvalue = '';
                    $("#Submitbtn").addClass('disabled');
                }
                vm.modelconditionexpression = '';
                vm.conditionexpression = false;
                vm.checkbycondition = 0;
            }
            else if (elem != undefined) {
                if (vm.fieldadded) {
                    var checkindex = vm.InputValueArray.findIndex(x => x.FieldValueData === getdata[0].FieldValueData);
                    if (checkindex > -1) {
                        vm.InputValueArray.splice(checkindex, 0, {
                            FieldValueData: vm.FieldTableNameValue,
                            MatchIndex: vm.$t((elem.DisplayName != null && elem.DisplayName != "") ? elem.DisplayName : elem.FieldName).length,
                            SameName: 'Field_Added' + cursorpos,
                            CustomFieldId: elem.CustomFieldId
                        }
                        )
                        vm.InputValueExpressionByID = vm.InputValueArray.map(x => x.FieldValueData).join('')
                    }
                }
                else {
                    vm.InputValueArray.push({
                        FieldValueData: vm.FieldTableNameValue,
                        MatchIndex: vm.$t((elem.DisplayName != null && elem.DisplayName != "") ? elem.DisplayName : elem.FieldName).length,
                        SameName: 'Field_Added' + cursorpos,
                        CustomFieldId: elem.CustomFieldId
                    });
                    vm.InputValueExpressionByID = vm.InputValueArray.map(x => x.FieldValueData).join('')
                }
            }
            else {
                vm.InputValueExpressionByID = vm.InputValueArray.map(x => x.FieldValueData).join('')
            }
        },
        updatingvalue() {
            var vm = this;
            if (typeof vm.InputValueExpression == 'object') {
                var convertstr = String(vm.InputValueExpression)
                vm.InputValueExpression = convertstr;
            }
            else {
                vm.InputValueExpression = vm.FieldsValue;
            }
        },
        deleteuserinput(event) {
            var vm = this;
            const key = event.key;
            if (event.code === 'Space') { vm.AddSpace(); }
            else if (event.keyCode >= 9 && event.keyCode <= 45 || event.keyCode > 90 && event.keyCode <= 93 || event.keyCode >= 112 && event.keyCode <= 167) {
                event.preventDefault();
            }
            else if (key === 'Backspace' || event.keyCode === 8 || event.keyCode === 46) {
                var cursorposition = vm.GetCursorPoint();
                cursorposition = cursorposition - 1;
                let assignindex;
                vm.fieldatavalue.forEach(item => {
                    if (item.MatchIndex < cursorposition) {
                        assignindex = cursorposition - item.MatchIndex;
                        cursorposition = assignindex;
                    }
                    else {
                        if (cursorposition != 0) {
                            let getindexofexp = vm.fieldatavalue.findIndex(x => x.SameName === item.SameName);
                            let getfieldindex = vm.FieldData.findIndex(f => f.CustomFieldId === item.CustomFieldId);
                            if (getindexofexp > -1) {
                                vm.fieldatavalue.splice(getindexofexp, 1);
                                vm.InputValueArray.splice(getindexofexp, 1);
                                if (getfieldindex > -1) {
                                    vm.FieldData.splice(getfieldindex, 1);
                                }
                                if (vm.fieldatavalue.length > 0) {
                                    vm.FieldsValue = vm.fieldatavalue.map(x => x.FieldValueData).join('');
                                } else {
                                    vm.FieldsValue = vm.fieldatavalue.join('');
                                    vm.substringvalue = '';
                                }
                                cursorposition = 0;
                                vm.Functionkey = null;
                            }
                        }
                    }
                })
                vm.updatingvalue();
                vm.updatevaluebyid();
            }
            else {
                var cursorpos = vm.GetCursorPoint();
                var getdata = vm.SetValueOnCursorPosition(cursorpos);
                if (getdata.length > 1) {
                    var checkindex = vm.InputValueArray.findIndex(x => x.FieldValueData === getdata[0].FieldValueData);
                    if (checkindex > -1) {
                        vm.fieldatavalue.splice(checkindex, 0, {
                            FieldValueData: key, MatchIndex: key.length, SameName: 'User-input' + cursorpos
                        })
                        vm.FieldsValue = vm.fieldatavalue.map(x => x.FieldValueData).join('');
                        vm.InputValueArray.splice(checkindex, 0, {
                            FieldValueData: key, MatchIndex: key.length, SameName: 'User-input' + cursorpos
                        })
                    }
                } else {
                    vm.fieldatavalue.push({ FieldValueData: key, MatchIndex: key.length, SameName: 'User-input' + cursorpos });
                    vm.FieldsValue = vm.fieldatavalue.map(x => x.FieldValueData).join('');
                    vm.InputValueArray.push({ FieldValueData: key, MatchIndex: key.length, SameName: 'User-input' + cursorpos });
                }
                vm.updatingvalue();
                vm.updatevaluebyid();
            }
            vm.buttons[0].class = "btn btn-success mr-2 my-sm-0 disabled";
            event.preventDefault();
        },
        UserInputSelection(userinput) {
            var vm = this;
            vm.selecteduserinput = userinput.target.selectionStart;
            vm.selecteduserinputbyid = vm.InputValueArray.length;
        },
        ExpresssionDataValue(data, fieldsdata) {
            var vm = this;
            //vm.ResetExpression();
            data.forEach(dataitem => {
                vm.ExpressionIDvalue = dataitem.ExpressionID;
                // vm.modelfieldname = dataitem.ExpressionName;
                vm.FormSchema[0].Data[0].value = dataitem.ExpressionName;
                vm.fieldatavalue.push({ FieldValueData: dataitem.ExpressionDisplay, MatchIndex: dataitem.ExpressionDisplay.length, SameName: 'EditedExp' });
                vm.FieldsValue = vm.fieldatavalue.map(x => x.FieldValueData).join('');
                vm.InputValueArray.push({ FieldValueData: dataitem.Expression, MatchIndex: dataitem.Expression.length, SameName: 'EditedExp' })
                vm.InputValueExpressionByID = vm.InputValueArray.map(x => x.FieldValueData).join('')
                vm.updatingvalue();
            })
            fieldsdata.forEach(item => {
                vm.FieldData.push({ CustomFieldId: item.CustomFieldId, FieldName: item.FieldName, ColumnIndex: item.ColumnIndex });
            })
        },
        VerifyExpression() {
            var vm = this;
            if (vm.InputValueExpression == '') {
                vm.ShowAlert(vm.$t("Expressionemmty"), 'failure', true, vm.$t('Alert'));
            } else {
                var jsondata = {
                    "Info": {
                        "Expression": encodeURIComponent(vm.InputValueExpressionByID),
                        "MODULENAME": vm.customreportparameters.Modulename,
                        "SUBMODULECODE": vm.customreportparameters.Submodulecode,
                        "FunctionExpression": vm.Functionkey,
                        "FIELDS": vm.FieldData
                    }
                }
                var jsonvalue = `JsonData=${JSON.stringify(jsondata)}`;
                vm.isLoading = true;
                ExpressionDataService.VerifyExpressionFromDB(jsonvalue).then(res => {
                    if (res.data.Result == "True") {
                        vm.verifiedvaluedata = 1;
                        vm.isLoading = false;
                        vm.buttons[0].class = "btn btn-success mr-2 my-sm-0";
                        vm.verifiedvalue = true;
                    }
                    else {
                        vm.verifiedvaluedata = 0;
                        vm.isLoading = false;
                    }
                }).catch(err => {
                    console.log(err);
                })
            }
        },
        SubmitExpression() {
            var vm = this;
            var InavalidExpression = true;
            if (!vm.editexpressionvalue) {
                var checkeditfieldexist = vm.Columnlist.findIndex(x => x.FieldName === vm.FormSchema[0].Data[0].value);
                if (checkeditfieldexist != -1) {
                    InavalidExpression = false;
                    vm.ShowAlert(vm.$t("ExpressionExist"), 'failure', true, vm.$t('Alert'));
                } else { InavalidExpression = true }
            }
            if (InavalidExpression) {
                if (vm.verifiedvalue && vm.FormSchema[0].Data[0].value != '') {
                    this.$emit('VerifiedExpressionData', {
                        ExpressionID: vm.ExpressionIDvalue,
                        FieldValue: vm.FormSchema[0].Data[0].value,
                        VerifiedExpression: vm.InputValueExpressionByID,
                        ExpressionFieldArray: vm.FieldData,
                        ExpressionDisplay: vm.InputValueExpression,
                        Functionkey: vm.Functionkey,
                        IsEdit: vm.editexpressionvalue,
                        IsExpExist: vm.AlreadyExistExpression
                    })
                    vm.verifiedvalue = false;
                }
            }
        },
        CancelExpressionUI() {
            var vm = this;
            vm.close();
        },
        closed() {
            $("#verifiedid").alert('close');
        },
        GetCursorPoint(e) {
            var vm = this;
            var cursorpos1 = document.getElementById("txtarea");
            cursorpos1.focus();
            var cursorpos = document.getElementById("txtarea").selectionStart;
            cursorpos = cursorpos + 1;
            return cursorpos;
        },
        SetValueOnCursorPosition(cursorpos) {
            var vm = this;
            var returndata = []
            var assignindex;
            vm.InputValueArray.forEach(item => {
                if (item.MatchIndex < cursorpos) {
                    assignindex = cursorpos - item.MatchIndex;
                    cursorpos = assignindex;
                }
                else if (item.MatchIndex > cursorpos || cursorpos == 1) {
                    returndata.push(item)
                }
            })
            return returndata;
        },
        AddConditionExpression() {
            var vm = this;
            vm.conditionexpression = true;
            if (vm.Functionkey == 'Date add' && vm.GetCommaSeparatedValue == 'CommaSeparatedValue') {
                vm.conditionexpression = false;
                vm.checkbycondition = vm.checkbycondition + 1;
            }
            vm.Addfields(vm.modelconditionexpression);
        },
        DescriptionForFunction() {
            var vm = this;
            if (vm.Functionkey == 'Date diff') {
                vm.ShowDescriptionForFunction = 'Interval(Year,Month,Day)';
            }
            else if (vm.Functionkey == 'Date add') {
                vm.ShowDescriptionForFunction = 'Interval(Year,Month,Day) Number(1,2,3,nth.)';
            }
            else if (vm.Functionkey == 'Date name') {
                vm.ShowDescriptionForFunction = 'Interval(Year,Month,Day)';
            }
            else if (vm.Functionkey == 'Date part') {
                vm.ShowDescriptionForFunction = 'Interval(Year,Month,Day)';
            }
            else if (vm.Functionkey == 'If') {
                vm.ShowDescriptionForFunction = 'BooleanExpression,True Value, False Value';
            }
        },
        ListFiltering() {
            var vm = this;
            var filterinput = vm.MainSearchmodel;
            if (filterinput != null && filterinput != "") {
                $(".column-collapse").addClass("show");
            }
            else {
                $(".column-collapse").removeClass("show");
            }
            var searchcol = [];
            var searchfilter = [];
            vm.modulelisting.forEach((item, index) => {
                vm.modulelisting[index].forEach((indexitem) => {
                    searchfilter.push(indexitem);
                    searchcol = searchfilter.filter(searchitem => {
                        var displayMainColname = (searchitem.DisplayName != null) ? searchitem.DisplayName : searchitem.FieldName;
                        return displayMainColname.replace(/\W/g, "").toLowerCase().includes(filterinput.replace(/\W/g, "").toLowerCase());
                    })
                })
                vm.searchColumnfilter[index] = searchcol;
                searchfilter = [];
            })
            let count = 0;
            for (let filteritem in vm.searchColumnfilter) {
                if (vm.searchColumnfilter[filteritem].length > 0) {
                    count++;
                }
            }
            if (count > 0) {
                vm.SearchNoRecord = false
            } else { vm.SearchNoRecord = true; }
            return vm.searchColumnfilter;
        }
    }
}
</script>
