<script>
import DataReportService from "@/services/DataService";
import draggable from "vuedraggable";
import ReportPreview from '../Add_Report/ReportPreview.vue';
import CalculatedUI from "../../../components/Common/Modal/modalCalculatedFieldUI.vue";
import moment from 'moment'

export default {
    components: {
        draggable, ReportPreview, CalculatedUI
    },
    data() {
        return {
            isLoading: false,
            modulelisting: [],
            modulelistingforfilter: [],
            showModuleListForField: true,
            showModuleListForFilter: false,
            reqFrom: 'Reporting',
            submodulecode: null,
            modulename: null,
            maindiv: "col-6 pl-0 float-left",
            expandcollapsediv: "bottom_filter_button",
            showdiv: "mb-0 d-none",
            fielddata: [],
            Grouplist: [],
            Columnlist: [],
            Filterlist: [],
            viewId: null,
            ReportName: null,
            fieldpan: 'col-6 p-0 float-left',
            modulelistingmodel: null,
            customreportparameters: null,
            tempreportid: null,
            saveclass: "card d-none",
            editclass: "card",
            savereportname: null,
            Showsettingsview: false,
            showdiv1: false,
            dateformate: [],
            GetOperator: [],
            customField: null,
            dragcount: 1,
            templates: ['Match Any Value', 'Match All Value'],
            category: 'Match All Value',
            ReportPreviewListing: [],
            previewdata: [],
            itemclass: 'd-none',
            Groupsearchmodel: '',
            ColumnSearchmodel: '',
            MainSearchmodel: '',
            //filtergroup: [],
            submodulename: [],
            submodulecodename: [],
            test1: [],
            PageNumber: 1,
            PAGESIZE: 10,
            selectvalue: '',
            selecteddatatype: '',
            texttypevalue1: false,
            numbertypevalue1: false,
            datetypevalue1: false,
            datetypevalue2: false,
            timetypevalue: false,
            timetypevalue2: false,
            multiselectvalue: false,
            numbertypevalue2: false,
            texttypevalue2: false,
            savedOperatorName: null,
            drag: false,
            GetDateFromDB: [],
            modeldisplayname: null,
            modeldateformat: '',
            modeldecimalplaces: null,
            modeluseseparator: null,
            modelusecurrency: null,
            FilterCount: 1,
            valueTypeText: '',
            valueTypeNumber: '',
            valueTypeNumber2: '',
            valueTypeFirstdate: '',
            valueTypeSeconddate: '',
            Displaymultiplevalue: '',
            displayvalue2: '',
            ValueDisplaytext: '',
            ValueDisplaytext2: '',
            ValueDisplaynumber: '',
            ValueDisplaynumber2: '',
            ValueDisplaydate: '',
            ValueDisplaydate2: '',
            ValueDisplayTime: '',
            ValueDisplayTime2: '',
            selectedvalue: '',
            RequestFilterJosn: [],
            CurrentOperatorSelected: null,
            filtertype: null,
            operator_id: null,
            mode: 'tags',
            dropdwonvalues: [],
            options: [],
            searchable: true,
            createOption: true,
            SelectedoperatorMappedDatatype: '',
            commaSepratedSelectedValues: '',
            selectedOPValue: '',
            GraphJson: [],
            sortingData: null,
            graphData: null,
            showReportPreview: false,
            editjsondata: false,
            valueKey: '',
            valuekey2: '',
            FirstOperand: '',
            FirstOperandValue1: '',
            SecondOperand: '',
            SecondOperandValue1: '',
            Datatype: '',
            operator: '',
            DisplayName: '',
            showcalculatedUI: false,
            CheckExpressionFeild: false,
            ExpressionArray: [],
            ExpressionFieldsArray: [],
            saveJsonReport: [],
            editexpressionvalue: false,
            CalculatedUIEnabled: false,
            searchColumnfilter: [],
            LevelOfGrouping: null,
            ExpFieldDataArray: [],
            ExpDataArray: [],
            setvalue: 0,
            ChartGroupingArray: [],
            GroupingArray: [],
            PdfJsonReport: [],
            FilterActiveForMandatory: false,
            SearchNoRecord: false,
            IsMandatoryforFilterAfterReset: false,
            ArrayforFilterpopup: [],
            modelFilterDDLValue: '',
            DDLForFilter: false,
            selctRuleForFilter: '',
            IsForPreview: 1,
            IsDDLValueInFilter: false,
            GetSubModuleList: [],
            showRowLevelFunction:false,
            sortingresetvalue:false,
            AlreadyExistExpression:{},
            FormSchema:[
                {
                layoutType: "single",
                Data: [ 
                        {
                            astype: "MultiSelectField",
                            label: '',
                            name: "Select",
                            value: "",
                            mode:"tag",
                            placeholder: "Select",
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: "",
                        },
                    ]
                }
            ]
        }
    },
    created: async function () {
        var vm = this;
        $(".fade").addClass("show");
        if (this.$route.params.Modulename != undefined) {
            vm.customreportparameters = this.$route.params;
            vm.storeParameters();
        } else { vm.GetStoredParameters(); }
        await vm.GetList(vm.customreportparameters);
        if (vm.customreportparameters.ReportID != null || vm.customreportparameters.ReportID != undefined) {
            await vm.GetReportDetailsByReportId();
            if (vm.IsDDLValueInFilter) {
                setTimeout(function () {
                    vm.showReportPreview = true;
                }, 1000)
            } else { vm.showReportPreview = true; }
        }
        vm.filterRulechange();
    },
    computed: {
        filtergroup() {
            var vm = this;
            return vm.Columnlist.filter(item => {
                var displayname = (item.DisplayName != null) ? item.DisplayName : item.FieldName;
                return displayname.replace(/\W/g, "").toLowerCase().includes(vm.Groupsearchmodel.replace(/\W/g, "").toLowerCase());
            })
        },
        filterColumngroup() {
            var vm = this;
            var searchColumn = [];
            var searchfilter = [];
            var GetSubModuleName = null;
            vm.modulelisting.forEach((item, index) => {
                vm.modulelisting[index].forEach((indexitem) => {
                    searchfilter.push(indexitem);
                    searchColumn = searchfilter.filter(searchitem => {
                        var displaycolname = (searchitem.DisplayName != null) ? searchitem.DisplayName : searchitem.FieldName;
                        return displaycolname.replace(/\W/g, "").toLowerCase().includes(vm.ColumnSearchmodel.replace(/\W/g, "").toLowerCase());
                    })
                    searchColumn.forEach(SearchItem => {
                        GetSubModuleName = vm.modulelisting[index].find(x => x.CustomFieldId === SearchItem.CustomFieldId)
                        if (GetSubModuleName != undefined) {
                            GetSubModuleName.MainFieldname = vm.submodulename[index];
                        }
                    })
                })
            })
            return searchColumn;
        }
    },
    methods: {
        storeParameters() {
            var vm = this;
            localStorage.setItem("SavedParameters", JSON.stringify(vm.customreportparameters));
        },
        GetStoredParameters() {
            var vm = this;
            vm.customreportparameters = JSON.parse(localStorage.getItem("SavedParameters"));
        },
        async GetFilterSelectDDL(CustomFieldId) {
            var vm = this;
            vm.options = [];
            var Getddldata = [];
            // vm.isLoading = false;
            // if (vm.operator_id == 10 || vm.operator_id == 27) {
            //     vm.isLoading = true;
            // }
            vm.isLoading = true;
            await DataReportService.GetFilterDDLselectvalue(CustomFieldId).then(response => {
                if (response.data.length > 0) {
                    // vm.options = response.data.map(item => {
                    //     return item.name != undefined ? item.name : item[FieldName]
                    // });
                    vm.FormSchema[0].Data[0].config.options = [];
                    Getddldata = response.data;
                    Getddldata.forEach(ddlitem => {
                        vm.options.push({
                            value: ddlitem.value,
                            name: ddlitem.name
                        })
                    })
                    vm.FormSchema[0].Data[0].config.options = vm.options;
                    vm.isLoading = false;
                } else { vm.isLoading = false; }
            }).catch(err => {
                console.log(err)
            })
        },
        async GetReportDetailsByReportId() {
            var vm = this;
            vm.isLoading = true;
            var params = `ReportId=${vm.customreportparameters.ReportID}&IsPaged=1`;
            await DataReportService.GetReportDetailsbyId(params).then(async response => {
                var checkfields = [];
                if (response.data != null && response.data != undefined && response.data != '') {
                    checkfields = response.data.JSON_To_Create_ReportListing[0].Info.FIELDS;
                }
                if (response.data != null && response.data != undefined && response.data != '' && checkfields.length != 0) {
                    vm.BindFeilds(response.data.JSON_To_Create_ReportListing[0].Info.FIELDS);
                    await vm.BindFilters(response.data.JSON_To_Create_ReportListing[0].Info.FILTERS);
                    vm.BindGROUP(response.data.JSON_To_Create_ReportListing[0].Info.GROUP);
                    vm.sortingData = response.data.JSON_To_Create_ReportListing[0].Info.SORTING
                    vm.graphData = response.data.JSON_To_Create_ReportListing[0].Info.GRAPH;
                    vm.ExpressionArray = response.data.JSON_To_Create_ReportListing[0].Info.EXPRESSION;
                    vm.ExpressionFieldsArray = response.data.JSON_To_Create_ReportListing[0].Info.EXPRESSIONFIELDSARRAY;
                    vm.editexpressionvalue = true;
                    if (response.data.ReportListing == 1) {
                        vm.editjsondata = true;
                        vm.saveJsonReport = response.data.JSON_To_Create_ReportListing[0];
                        vm.PdfJsonReport = response.data.JSON_To_Create_ReportListing[0];
                    }
                } else {
                    if (checkfields.length == 0) {
                        vm.CheckIsMandartoryForFilter();
                    }
                    vm.isLoading = false;
                }
            }).catch(err => {
                console.log(err);
            })
        },
        BindFeilds(FIELDS) {
            var vm = this;
            FIELDS.forEach((item) => {
                vm.Columnlist.push(item);
                vm.modulelisting.forEach((mitem, index) => {
                    let indexFound = vm.modulelisting[index].findIndex(element => { return element.CustomFieldId === item.CustomFieldId })
                    if (indexFound != -1) {
                        vm.modulelisting[index].splice(indexFound, 1);
                    }
                })
            })
            if (vm.Columnlist.length > 0) {
                vm.CalculatedUIEnabled = true;
            }
        },
        bindpouupvaluefordatetime(item) {
            var vm = this;
            if (item.ColumnFieldDataCode == 'datetime' || item.ColumnFieldDataCode == 'date') {
                if (item.operator_display_name == 'Between') {
                    vm.valueKey = 'valueTypeFirstdate'
                    vm.valuekey2 = 'valueTypeSeconddate'
                }
                else {
                    vm.valueKey = 'valueTypeFirstdate'
                }
            }
            if (item.ColumnFieldDataCode == 'time') {
                if (item.operator_display_name == 'Between') {
                    vm.valueKey = 'ValueDisplayFirstTime'
                    vm.valuekey2 = 'ValueDisplaySecondTime'
                }
                else {
                    vm.valueKey = 'ValueDisplayFirstTime'
                }
            }
        },
        bindfilterdata(FILTERS) {
            var vm = this;
            vm.Filterlist = FILTERS.map((item, index) => {
                var GetDDLValueinEdit = '';
                var GetDDLTempVal = '';
                if (item.ColumnFieldDataCode == 'select' && item.PickListOption == 'Lookup') {
                    var GetDDLValueinEditArr = [];
                    if (item.OperatorExpression.includes('IN') || item.OperatorExpression.includes('NOT IN')) {
                        var GetDDLValueinEditMulti = [];
                        GetDDLValueinEditArr = item.Value1.split(',');
                        GetDDLValueinEditArr.forEach(DDLEditItem => {
                            GetDDLValueinEdit = vm.options.find(i => i.value === DDLEditItem.replace(/"|'/g, ''))
                            GetDDLValueinEditMulti.push(GetDDLValueinEdit.name);
                        })
                        GetDDLTempVal = GetDDLValueinEditMulti?.join();
                        vm.valueKey = 'Displaymultiplevalue';
                    }
                    else {
                        GetDDLValueinEdit = vm.options.find(i => i.value === item.Value1);
                        GetDDLTempVal = GetDDLValueinEdit?.name;
                        vm.valueKey = 'DropDownValue';
                    }
                }
                else if (item.ColumnFieldDataCode == 'decimal' || item.ColumnFieldDataCode == 'bigint' || item.ColumnFieldDataCode == 'int') {
                    if (item.operator_display_name == 'Between') {
                        vm.valueKey = 'valueTypeNumber';
                        vm.valuekey2 = 'valueTypeNumber2';
                    }
                    else {
                        vm.valueKey = 'valueTypeNumber';
                    }
                }
                else if (item.ColumnFieldDataCode == 'string') {
                    if (item.OperatorExpression.includes('IN') || item.OperatorExpression.includes('NOT IN')) {
                        vm.valueKey = 'valueTypeText';
                        vm.valuekey2 = 'valueTypeText2';
                    } else { vm.valueKey = 'valueTypeText' };
                }
                vm.bindpouupvaluefordatetime(item);
                let moduleIndex = vm.modulelistingforfilter.findIndex(moduleItem => {
                    return moduleItem.find(field => {
                        return field.CustomFieldId == item.CustomFieldId
                    })
                })
                let indexFound = vm.modulelistingforfilter[moduleIndex].findIndex(field => { return field.CustomFieldId == item.CustomFieldId })
                let returnObject = {}
                if (indexFound != -1) {
                    returnObject = {
                        FieldName: vm.modulelistingforfilter[moduleIndex][indexFound].FieldName,
                        CustomFieldId: item.CustomFieldId,
                        ModuleIndex: moduleIndex,
                        ActualDataType: item.ActualDataType,
                        FilterCount: index + 1,
                        FilterTypeAndorOr: vm.filtertype,
                        OPERATOR_ID: item.OPERATOR_ID,
                        OperatorDisplayName: item.operator_display_name,
                        IsMandatoryforFilter: item.IsMandatoryforFilter,
                        FieldIcons: item.FieldIcons,
                        DisplayName: item.DisplayName,
                        ColumnFieldDataCode: item.ColumnFieldDataCode,
                        PickListOption: item.PickListOption,
                        Calculated_Field: false
                    }
                    returnObject[vm.valuekey2] = item.Value2;
                    returnObject[vm.valueKey] = GetDDLTempVal == '' ? item.Value1.replace(/"|'/g, '') : GetDDLTempVal;
                    vm.modulelistingforfilter[moduleIndex].splice(indexFound, 1);
                }
                return returnObject;
            })
        },
        CheckforIncludeDataMatchesForFilter() {
            var vm = this;
            if (vm.filtertype == 'AND') {
                vm.category = 'Match All Value';
            } else {
                vm.category = 'Match Any Value'
            }
        },
        BindFilters: async function (FILTERS) {
            var vm = this;
            if (FILTERS.length > 0) {
                FILTERS.map(async (item, index) => {
                    if (item.ColumnFieldDataCode == 'select' && item.PickListOption == 'Lookup') {
                        vm.IsDDLValueInFilter = true;
                        await vm.GetFilterSelectDDL(item.CustomFieldId);
                    }
                })
                if (vm.IsDDLValueInFilter) {
                    setTimeout(function () {
                        vm.filtertype = FILTERS[0].FilterTypeAndorOr;
                        vm.CheckforIncludeDataMatchesForFilter();
                        vm.bindfilterdata(FILTERS);
                        vm.RequestFilterJosn = FILTERS.map((item, index) => {
                            let indexFound = vm.Filterlist.findIndex(field => { return field.CustomFieldId == item.CustomFieldId })
                            let returnObject = {}
                            if (indexFound != -1) {
                                returnObject = {
                                    CustomFieldId: item.CustomFieldId,
                                    FieldName: vm.Filterlist[indexFound].FieldName,
                                    ActualDataType: item.ActualDataType,
                                    FilterCount: index + 1,
                                    OperatorExpression: item.OperatorExpression,
                                    FilterTypeAndorOr: item.FilterTypeAndorOr,
                                    OPERATOR_ID: item.OPERATOR_ID,
                                    Value1: item.Value1,
                                    Value2: item.Value2,
                                    ColumnFieldDataCode: item.ColumnFieldDataCode,
                                    PickListOption: item.PickListOption
                                }
                            }
                            return returnObject;
                        })
                    }, 1000)
                }
                else {
                    vm.filtertype = FILTERS[0].FilterTypeAndorOr;
                    vm.CheckforIncludeDataMatchesForFilter();
                    vm.bindfilterdata(FILTERS);
                    vm.RequestFilterJosn = FILTERS.map((item, index) => {
                        let indexFound = vm.Filterlist.findIndex(field => { return field.CustomFieldId == item.CustomFieldId })
                        let returnObject = {}
                        if (indexFound != -1) {
                            returnObject = {
                                CustomFieldId: item.CustomFieldId,
                                FieldName: vm.Filterlist[indexFound].FieldName,
                                ActualDataType: item.ActualDataType,
                                FilterCount: index + 1,
                                OperatorExpression: item.OperatorExpression,
                                FilterTypeAndorOr: item.FilterTypeAndorOr,
                                OPERATOR_ID: item.OPERATOR_ID,
                                Value1: item.Value1,
                                Value2: item.Value2,
                                ColumnFieldDataCode: item.ColumnFieldDataCode,
                                PickListOption: item.PickListOption
                            }
                        }
                        return returnObject;
                    })
                }
            }
        },
        BindGROUP(GROUP) {
            var vm = this;
            vm.Grouplist = [];
            GROUP.forEach((item) => {
                vm.Grouplist.push(item);
            })
        },
        openSetting: function (element, event) {
            debugger
            var vm = this;
            if (element.Calculated_Field) {
                vm.showcalculatedUI = true;
                vm.editexpressionvalue = true;
                vm.AlreadyExistExpression = {};
                if (vm.ExpressionFieldsArray.length > 0 || vm.ExpressionArray.length > 0) {
                    let expIndex = -1;
                    vm.ExpDataArray = [];
                    vm.ExpFieldDataArray = [];
                    for (var i = 0; i <= vm.ExpressionArray.length; i++) {
                        if (vm.ExpressionArray[i].ColumnIndex === element.ModuleIndex) {
                            vm.ExpDataArray.push(vm.ExpressionArray.find(x => x.ColumnIndex === element.ModuleIndex))
                            break;
                        }
                    }
                    vm.ExpressionFieldsArray.forEach(item => {
                        var temparr = [];
                        temparr.push(item);
                        var checkexpfieldvalue = temparr.find(x => x.ColumnIndex === element.ModuleIndex);
                        if (checkexpfieldvalue != undefined) {
                            vm.ExpFieldDataArray.push(checkexpfieldvalue);
                        }
                    })
                }
                $("#showcalculatedfield").modal("show");
                setTimeout(function () {
                    $("#showcalculatedfield").modal('show');
                    setTimeout(function () {
                        vm.SetBootstraptoolTip();
                    }, 200);
                }, 1000);
                if (vm.editexpressionvalue) {
                    vm.AlreadyExistExpression = element;
                    vm.$refs.Calculated?.ExpresssionDataValue(vm.ExpDataArray, vm.ExpFieldDataArray);
                }
            } else {
                vm.modeldisplayname = vm.GetLocalizedValue(element.DisplayName != null && element.DisplayName != '' ? element.DisplayName : element.FieldName);
                vm.modeldateformat = element.DateFormat;
                vm.modeldecimalplaces = element.DecimalPlace;
                vm.modeluseseparator = element.IsUseSeprator;
                vm.modelusecurrency = element.IsCurrencySymbol;
                $(".setting-div-box").width('0px');
                $(event.target).closest(".parentdiv").find(".setting-div-box").width('500px');
                vm.GetDateFromDetail();
            }
         
        },
        CloseSetting: function (event) {
            var vm = this;
            vm.modeldisplayname = null;
            vm.modeldateformat = '';
            vm.modeldecimalplaces = null;
            vm.modeluseseparator = null;
            vm.modelusecurrency = null;
            $(event.target).closest(".setting-div-box").width('0px');
        },
        ClosePopFilter: function () {
            var vm = this;
            $(".filter-list-box").width('0px').css("overflow", "hidden");
            vm.texttypevalue1 = false;
            vm.numbertypevalue1 = false;
            vm.datetypevalue1 = false;
            vm.datetypevalue2 = false;
            vm.multiselectvalue = false;
            vm.numbertypevalue2 = false;
            vm.texttypevalue2 = false;
            vm.DDLForFilter = false;
            vm.timetypevalue = false;
            vm.timetypevalue2 = false;
            vm.ValueDisplaytext = '';
            vm.ValueDisplaytext2 = '';
            vm.ValueDisplaynumber = '';
            vm.ValueDisplaynumber2 = '';
            vm.ValueDisplaydate = '';
            vm.ValueDisplaydate2 = '';
            vm.ValueDisplayTime = '';
            vm.ValueDisplayTime2 = '';
            vm.Displaymultiplevalue = '';
            vm.dropdwonvalues = [];
            vm.FormSchema[0].Data[0].value= [];
            vm.modelFilterDDLValue = '';
            vm.SecondOperand = '';
            vm.SecondOperandValue1 = '';
        },
        CheckFeildOrFilter() {
            var vm = this;
            let isFieldsActive = vm.$refs.FIELDS.active
            if (isFieldsActive) {
                return 'FIELD'
            }
            else {
                return 'Filters';
            }
        },
        GetList: async function (customreportparameters) {
            var vm = this;
            if (customreportparameters.Modulename != undefined && customreportparameters.Submodulecode != undefined && customreportparameters.ReportName != null) {
                var params = `moduleName=${customreportparameters.Modulename}&subModuleCode=${customreportparameters.Submodulecode}&viewId&reqFrom=${vm.reqFrom}`;
                await DataReportService.GetListFields(params).then(response => {
                    vm.submodulecodename = response.data.AVAILABLE_FIELDS[0];
                    vm.submodulecodename.forEach((item) => {
                        vm.submodulename.push(item.SubModule);
                        vm.modulelisting.push([...item.CustomFields.sort(function(a, b){if(a.DisplayName < b.DisplayName) { return -1; }if(a.DisplayName > b.DisplayName) { return 1; }return 0; })]);
                        vm.modulelistingforfilter.push([...item.CustomFields.sort(function(a, b){if(a.DisplayName < b.DisplayName) { return -1; }if(a.DisplayName > b.DisplayName) { return 1; }return 0; })]);
                        vm.GetSubModuleList.push([...item.CustomFields]);
                    })
                    if (customreportparameters.NewReportInserted == "Save" && customreportparameters.ReportID != undefined || vm.IsMandatoryforFilterAfterReset) {
                        vm.CheckIsMandartoryForFilter();
                    }
                    setTimeout(function () {
                        vm.SetScrolbar('dark-thick', '', 'customScrollForLeftBar')
                    }, 1000);
                }).catch(err => { console.log(err); })
                vm.submodulecode = customreportparameters.Submodulecode;
                vm.ReportName = customreportparameters.ReportName;
            }
        },
        CheckIsMandartoryForFilter: function () {
            var vm = this;
            vm.modulelisting.forEach((Fields, index) => {
                vm.modulelisting[index].forEach(Fieldsitem => {
                    if (Fieldsitem.IsMandatoryforFilter) {
                        var CheckFields = vm.modulelisting[index].find(x => x.CustomFieldId === Fieldsitem.CustomFieldId)
                        if (CheckFields.ActualDataType == "datetime") {
                            vm.FilterActiveForMandatory = true;
                            vm.SelectedoperatorMappedDatatype = CheckFields.dttype[0].DataTypeCode;
                            vm.ValueDisplaydate = moment(moment().subtract(30, 'days')).format("YYYY-MM-DD");
                            // var date = new Date(); // Now
                            // date.setDate(date.getDate() - 30); // Set now - 30 days as the new date
                            // vm.ValueDisplaydate = date;
                            vm.savedOperatorName = "Greater Than";
                            vm.CurrentOperatorSelected = '>';
                            vm.operator_id = 1;
                            vm.AddFilter(CheckFields, index);
                            vm.applySetting(CheckFields.CustomFieldId);
                        }
                    }
                })
            })
        },
        collapsedclick: function () {
            var vm = this
            vm.maindiv = "col-6 pl-0 float-left d-none";
            vm.showdiv = "mb-0";
            vm.fieldpan = 'col-6 p-0 float-left leftpancollapse col-11'
        },
        expandclick: function () {
            var vm = this
            vm.maindiv = "col-6 pl-0 float-left ";
            vm.showdiv = "mb-0 d-none";
            vm.fieldpan = 'col-6 p-0 float-left';
        },
        editreport: function () {
            var vm = this;
            vm.editclass = "card d-none";
            vm.saveclass = "card";
            var params = `&Report_id=${vm.customreportparameters.ReportID}`;
            DataReportService.Editdyanmicreport(params).then(response => {
                vm.savereportname = response.data.ReportName;
            }).catch(err => { console.log(err); })
        },
        savereport: function () {
            var vm = this;
            vm.editclass = "card";
            vm.saveclass = "card d-none";
            var params = `ReportName=${vm.savereportname}&Report_Id=${vm.customreportparameters.ReportID}&Folder_Id=${vm.customreportparameters.Folder_Id}&Module_Id=${vm.customreportparameters.Module_Id}&Sub_Module_Id=${vm.customreportparameters.Sub_Module_Id}&Ref_Code=${vm.customreportparameters.Submodulecode}&Version=${vm.customreportparameters.Version}`;
            DataReportService.AddDynamicreport(params).then(response => {
                if (response.data[0].Status == 'Update') {
                    vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("ReportName")]), 'success', true, vm.$t('Alert'));
                    vm.ReportName = vm.savereportname;
                } else if (response.data[0].Status == 'Exists') {
                    vm.savereportname = null;
                    vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("ReportName")]), 'failure', true, vm.$t('Alert'));
                } else {
                    vm.savereportname = null;
                    vm.ShowAlert(vm.$t("SaveError", [vm.$t("ReportName")]), 'failure', true, vm.$t('Alert'));
                }
            }).catch(err => { console.log(err); })
        },
        PreviewJson: async function (sortColummnarray, BindGraph, GraphDataValue) {
            var vm = this;
            var sortingArray = [];
            vm.sortingresetvalue=true;
            vm.isLoading = true;
            let CheckSameNameExist = vm.MatchIfSameNameExist();
            if (!CheckSameNameExist) {
                let CheckValidSorting = vm.CheckForValidSorting(sortColummnarray);
                if (CheckValidSorting == 0) {
                    for (var i in sortColummnarray) {
                        if (sortColummnarray[i].sortcolummnId != undefined && sortColummnarray[i].sortcolummnId != "") {
                            sortingArray.push(
