                                "EXPRESSIONFIELDSARRAY": vm.ExpressionFieldsArray
                            }
                        }
                    }
                    vm.showReportPreview = true;
                    vm.saveJsonReport = temp;
                    var jsondata = JSON.stringify(temp);
                    await DataReportService.PreviewReport(jsondata).then((response) => {
                        var checkdata = [];
