        },
        Addfields: function (element, event, index, MainFieldname) {
            var vm = this;
            let isFieldActive = vm.$refs.FIELDS.active;
            var ColumnDisplayOrder;
            if (vm.Columnlist.length < 20) {
                if (isFieldActive) {
                    if (vm.Columnlist.length < 1) {
                        ColumnDisplayOrder = vm.Columnlist.length
                    }
                    else {
                        const getlastarrayvalue = vm.Columnlist[vm.Columnlist.length - 1];
                        ColumnDisplayOrder = getlastarrayvalue.Display_Order + 1;
                    }
                    if (element.hasOwnProperty("dttype")) {
                        const checkAlredyExist = vm.Columnlist.findIndex(object => object.CustomFieldId === element.CustomFieldId);
                        if (checkAlredyExist === -1) {
                            vm.Columnlist.push({
                                CustomFieldId: element.CustomFieldId, DisplayName: element.DisplayName, FieldName: element.FieldName, ModuleIndex: index, ColumnFieldDataCode: element.dttype[0].DataTypeCode, Display_Order: ColumnDisplayOrder, Calculated_Field: false, MainFieldname: MainFieldname
                                , FieldIcons: element.dttype[0].icon
                            });
                            if (event.removed == undefined) {
                                vm.modulelisting[index].splice(vm.modulelisting[index].indexOf(element), 1);
                            }
                        }
                    } else {
                        const checkAlredyExist = vm.Columnlist.findIndex(object => object.CustomFieldId == element.CustomFieldId);
                        if (checkAlredyExist === -1) {
                            vm.Columnlist.push({
                                CustomFieldId: element.CustomFieldId, DisplayName: element.DisplayName, FieldName: element.FieldName, ModuleIndex: index, ColumnFieldDataCode: element.ColumnFieldDataCode, Display_Order: ColumnDisplayOrder, Calculated_Field: false, MainFieldname: MainFieldname
                                , FieldIcons: element.FieldIcons
                            });
                            if (event.removed == undefined) {
                                vm.modulelisting[index].splice(vm.modulelisting[index].indexOf(element), 1);
                            }
                            // $(event.target).closest(".parent-element").remove();
                        }

                    }
                }
            } else {
                vm.ShowAlert(vm.$t("AddCustomFields"), 'failure', true, vm.$t('Alert'));
            }
            vm.CalculatedUIEnabled = true;
            vm.searchColumnfilter = []
            vm.MainSearchmodel = '';
        },
