            if (!IsGraphColumn) {
                if (!element.Calculated_Field) {
                    let tagIndex = vm.Columnlist.findIndex(x => x.CustomFieldId == element.CustomFieldId);
                    if (tagIndex > -1) {
                        vm.Columnlist.splice(tagIndex, 1);
                        vm.searchColumnfilter = [];
                        $(".setting-div-box").width('0px');
                    }
                }
                let tagIndex1 = vm.Grouplist.findIndex(x => x.CustomFieldId == element.CustomFieldId);
                if (tagIndex1 > -1) {
                    vm.Grouplist.splice(tagIndex1, 1);
                }
                if (vm.Columnlist.length == 0) {
                    vm.previewdata = [];
                    vm.CalculatedUIEnabled = false;
                }
                if (!element.Calculated_Field) {
                    vm.modulelisting[element.ModuleIndex].push({
                        CustomFieldId: element.CustomFieldId, DisplayName: element.DisplayName, FieldName: element.FieldName, ColumnFieldDataCode: element.ColumnFieldDataCode, Display_Order: element.Display_Order, Calculated_Field: false, MainFieldname: element.MainFieldname
                        , FieldIcons: element.FieldIcons
                    });
                }
                vm.Groupsearchmodel = '';
                if (element.Calculated_Field) {
                    if (vm.ExpressionFieldsArray.length > 0 || vm.ExpressionArray.length > 0) {
                        let expIndex = -1;
                        let calctagIndex = vm.Columnlist.findIndex(x => x.ModuleIndex == element.ModuleIndex);
                        if (calctagIndex > -1) {
                            vm.Columnlist.splice(calctagIndex, 1);
                            vm.searchColumnfilter = [];
                        }
                        for (var i = 0; i <= vm.ExpressionArray.length; i++) {
                            if (vm.ExpressionArray[i].ColumnIndex === element.ModuleIndex) {
                                vm.ExpressionArray.splice(i, 1);
                                break;
                            }
                        }
                        vm.ExpressionFieldsArray.forEach(item => {
                            var checkindex = vm.ExpressionFieldsArray.findIndex(x => x.ColumnIndex === element.ModuleIndex)
                            if (checkindex > -1) {
                                vm.ExpressionFieldsArray.splice(checkindex, 1);
                            }
                        })
                    }
                }
                if (vm.sortingData != null && vm.sortingData.length > 0) {
                    // let sortindex = vm.sortingData.findIndex(x => x.CustomFieldId === element.CustomFieldId)
                    let sortindex = vm.sortingData.findIndex(x => x.Calculated_Field === element.Calculated_Field)
                    if (sortindex > -1) {
                        vm.sortingData.splice(sortindex, 1);
                        vm.$refs.reportdata?.EditSortingData();
                    }
                }
            }else{
                vm.ShowAlert(vm.$t("GraphFields"), 'warning', true, vm.$t('Alert'));
            }
        },
