        resetoldvalueinfilteroperator(element, IsBinding) {
            var vm = this;
            if (element.selectedOPValue != '' && IsBinding != '') {
                vm.dropdwonvalues = [];
                vm.FormSchema[0].Data[0].value= []
                vm.ValueDisplaytext = '';
