            }
        },
        checkvalue: function (operatorname, element, OperatorExpression, operatorid, IsBinding) {
            var vm = this;
            if (element.ColumnFieldDataCode === 'select' && element.PickListOption === 'Lookup') {
                vm.GetFilterSelectDDL(element.CustomFieldId, element.FieldName);
            }
            setTimeout(function () {
                vm.savedOperatorName = "";
                vm.SelectedoperatorMappedDatatype = element.ColumnFieldDataCode;
                vm.CurrentOperatorSelected = OperatorExpression;
                vm.savedOperatorName = operatorname;
                vm.operator_id = operatorid;
                if (element.ColumnFieldDataCode == 'string') {
                    if (operatorname == 'Between') {
                        vm.texttypevalue1 = true;
                        vm.texttypevalue2 = true;
                    } else if (operatorname == 'Is Empty' || operatorname == 'Is Not Empty') {
                        vm.texttypevalue1 = false;
                        vm.texttypevalue2 = false;
                    }
                    else {
                        vm.texttypevalue1 = true;
                        vm.texttypevalue2 = false;
                    }
                }
                if (element.ColumnFieldDataCode == 'decimal' || element.ColumnFieldDataCode == 'int' || element.ColumnFieldDataCode == 'bigint') {
                    if (operatorname == 'Between') {
                        vm.numbertypevalue1 = true;
                        vm.numbertypevalue2 = true;
                    } else if (operatorname == 'Is Empty' || operatorname == 'Is Not Empty') {
                        vm.numbertypevalue1 = false;
                        vm.numbertypevalue2 = false;
                    }
                    else {
                        vm.numbertypevalue1 = true;
                        vm.numbertypevalue2 = false;
                    }
                }
                if (element.ColumnFieldDataCode === 'select' && element.PickListOption === 'Lookup') {
                    if (operatorname == 'Is Empty' || operatorname == 'Is Not Empty') {
                        vm.DDLForFilter = false;
                        vm.multiselectvalue = false;
                        vm.texttypevalue1 = false;
                        vm.texttypevalue2 = false;
                    }
                    else if (OperatorExpression == 'IN' || OperatorExpression == 'NOT IN') {
                        vm.multiselectvalue = true;
                        vm.DDLForFilter = false;
                    } else {
                        vm.DDLForFilter = true;
                        vm.multiselectvalue = false;
                        vm.texttypevalue1 = false;
                        vm.texttypevalue2 = false;
                    }
                }
                if (element.ColumnFieldDataCode === 'date') {
                    if (operatorname == 'Between') {
                        vm.datetypevalue1 = true;
                        vm.datetypevalue2 = true;
                    } else if (operatorname == 'Is Empty' || operatorname == 'Is Not Empty' || operatorname == 'Is Off Day' || operatorname == 'Is Week End') {
                        vm.datetypevalue1 = false;
                        vm.datetypevalue2 = false;
                    }
                    else {
                        vm.datetypevalue1 = true;
                        vm.datetypevalue2 = false;
                    }
                }

                if (element.ColumnFieldDataCode === 'time') {
                    if (operatorname == 'Between') {
                        vm.timetypevalue = true;
                        vm.timetypevalue2 = true;
                    } else if (operatorname == 'Is Empty' || operatorname == 'Is Not Empty' || operatorname == 'Is Off Day' || operatorname == 'Is Week End') {
                        vm.timetypevalue = false;
                        vm.timetypevalue2 = false;
                    }
                    else {
                        vm.timetypevalue = true;
                        vm.timetypevalue2 = false;
                    }
                }

                if (IsBinding != undefined) {
                    if (operatorid != undefined && operatorname != undefined) {
                        vm.dropdwonvalues = [];
                        vm.FormSchema[0].Data[0].value= []
                        vm.ValueDisplaytext = '';
                        vm.ValueDisplaytext2 = '';
                        vm.ValueDisplaynumber = '';
                        vm.ValueDisplaynumber2 = '';
                        vm.ValueDisplaydate = '';
                        vm.ValueDisplaydate2 = '';
                        vm.modelFilterDDLValue = '';
                        vm.ValueDisplayTime = '';
                        vm.ValueDisplayTime2 = '';
                        if (element.OPERATOR_ID == operatorid) {
                            vm.FillFiltervalue(operatorid, element);
                        }
                        element = [];
                    }
                }
                else {
                    var checksameoperator = vm.Filterlist.find(x => x.OPERATOR_ID === operatorid);
                    if (checksameoperator != -1) {
                        vm.resetoldvalueinfilteroperator(element, IsBinding)
                    }
                }
            }, 1000);
        },
        GetOperatordata: function (element) {
            var vm = this;
            var CustomFieldId = element.CustomFieldId;
            var params = `dataType=${element.ColumnFieldDataCode}&customField=${CustomFieldId}`;
            DataReportService.GetDataOperator(params).then(response => {
                vm.GetOperator = response.data.DATA;
                vm.BindOperatorValues(CustomFieldId, element);
                if (element.OPERATOR_ID > 0) {
                    var checkIfExists = vm.GetOperator.filter((x) => {
                        return x.OPERATOR_ID == element.OPERATOR_ID
                    });
                    if (checkIfExists.length > 0) {
                        var IsBinding = true;
                        vm.isBinding = IsBinding;
                        vm.checkvalue(checkIfExists[0].OPERATOR_DISPLAY_NAME, element, checkIfExists[0].OPERATOR_EXPRESSION, element.OPERATOR_ID, checkIfExists[0].MP[0].OPERATION_DATA_TYPE, IsBinding)
                    }
                }
            })
        },
        popupvaluebind(Index, data) {
            var vm = this;
            if (data.ColumnFieldDataCode == 'string') {
                if (data.OperatorDisplayName == 'Equal to one of multiple possible values' || data.OperatorDisplayName == 'Not Equal to one of multiple possible values') {
                    vm.texttypevalue1 = true;
                    vm.ValueDisplaytext = this.Filterlist[Index].valueTypeText.split(',');
                    //  vm.value5 = true;
                    // vm.dropdwonvalues = this.Filterlist[Index].Displaymultiplevalue.split(',');
                }
                else {
                    vm.texttypevalue1 = true;
                    vm.ValueDisplaytext = this.Filterlist[Index].valueTypeText;
                }
            }
            if (data.ColumnFieldDataCode == 'select') {
                if (data.OperatorDisplayName == 'Equal to one of multiple possible values' || data.OperatorDisplayName == 'Not Equal to one of multiple possible values') {
                    vm.multiselectvalue = true;
                    vm.commaSepratedSelectedValues = this.Filterlist[Index].Displaymultiplevalue.split(',');
                }
                else {
                    vm.DDLForFilter = true;
                    var GetFilterddlvalue = vm.options.find(x => x.name === data.DropDownValue);
                    vm.modelFilterDDLValue = GetFilterddlvalue?.value;
                }
            }
            if (data.ColumnFieldDataCode == 'date') {
                if (vm.DisplayName == 'Between') {
                    vm.datetypevalue1 = true;
                    vm.datetypevalue2 = true;
                    vm.ValueDisplaydate = this.Filterlist[Index].valueTypeFirstdate;
                    vm.ValueDisplaydate2 = this.Filterlist[Index].valueTypeSeconddate;
                }
                else {
                    vm.datetypevalue1 = true;
                    vm.ValueDisplaydate = this.Filterlist[Index].valueTypeFirstdate;
                }
            }
            if (data.ColumnFieldDataCode == 'time') {
                if (vm.DisplayName == 'Between') {
                    vm.timetypevalue = true;
                    vm.timetypevalue2 = true;
                    vm.ValueDisplayTime = this.Filterlist[Index].ValueDisplayFirstTime;
                    vm.ValueDisplayTime2 = this.Filterlist[Index].ValueDisplaySecondTime;
                }
                else {
                    vm.timetypevalue = true;
                    vm.ValueDisplayTime = this.Filterlist[Index].ValueDisplayFirstTime;
                }
            }
        },
        BindOperatorValues(CustomFieldId, element) {
            var vm = this;
            vm.isBinding = false;
            var Index = this.Filterlist.findIndex(element => element.CustomFieldId == CustomFieldId)
            vm.DisplayName = this.Filterlist[Index].OperatorDisplayName;
            if (vm.DisplayName != null && vm.DisplayName != undefined && vm.DisplayName != '') {
                vm.operator = this.GetOperator.find(item => item.OPERATOR_DISPLAY_NAME == vm.DisplayName)
                vm.Filterlist[Index]['selectedOPValue'] = vm.operator.OPERATOR_ID;
                vm.Datatype = vm.operator.MP[0].OPERATION_DATA_TYPE;
                vm.popupvaluebind(Index, element);
                if (element.ColumnFieldDataCode == 'decimal' || element.ColumnFieldDataCode == 'int') {
                    if (vm.DisplayName == 'Between') {
                        vm.numbertypevalue1 = true;
                        vm.numbertypevalue2 = true;
                        vm.ValueDisplaynumber = this.Filterlist[Index].valueTypeNumber;
                        vm.ValueDisplaynumber2 = this.Filterlist[Index].valueTypeNumber2;
                    }
                    else {
                        vm.numbertypevalue1 = true;
                        vm.ValueDisplaynumber = this.Filterlist[Index].valueTypeNumber;
                    }
                }
            } else {
                vm.selectedOPValue = '';
            }
        },
        FilterJosnRquest(elem) {
            var vm = this;
            if ((elem.OperatorDisplayName == 'Not Match a character pattern' || elem.OperatorDisplayName == 'Match a character pattern') && vm.ValueDisplaytext == '') {
                vm.ShowAlert(vm.$t("Please enter some value in the text field"), 'warning', true, vm.$t('Alert'));
                return false;
            }
            if (vm.SelectedoperatorMappedDatatype == 'string') {
                if (vm.CurrentOperatorSelected == 'IN' || vm.CurrentOperatorSelected == 'NOT IN') {
                    vm.FirstOperand = vm.ValueDisplaytext
                    vm.FirstOperandValue1 = vm.ValueDisplaytext2;

                    if (elem.OperatorDisplayName == 'Equal to one of multiple possible values' || elem.OperatorDisplayName == 'Not Equal to one of multiple possible values') {
                        let Commaseprated = vm.ValueDisplaytext.split(',')


                        vm.FirstOperandValue1 = Commaseprated;
                    }
                }
                else {
                    vm.FirstOperand = '\'' + vm.ValueDisplaytext + '\'';
                    vm.FirstOperandValue1 = vm.ValueDisplaytext;
                }
            }

            else if (vm.SelectedoperatorMappedDatatype == 'decimal' || vm.SelectedoperatorMappedDatatype == 'int' || vm.SelectedoperatorMappedDatatype == 'bigint') {
                if (elem.OperatorDisplayName == 'Between') {
                    vm.FirstOperand = '\'' + elem.valueTypeNumber + '\'';
                    vm.FirstOperandValue1 = elem.valueTypeNumber;
                    vm.SecondOperand = '\'' + elem.valueTypeNumber2 + '\'';
                    vm.SecondOperandValue1 = elem.valueTypeNumber2;
                }
                else {
                    vm.FirstOperand = '\'' + elem.valueTypeNumber + '\'';
                    vm.FirstOperandValue1 = elem.valueTypeNumber;
                }
            }
            let operatoerExpressionResult = vm.ConvertOperatorToExpression(vm.CurrentOperatorSelected, vm.FirstOperand, vm.SecondOperand);
            const checkAlredyExist = vm.RequestFilterJosn.findIndex(item => item.CustomFieldId === elem.CustomFieldId);
            if (checkAlredyExist == -1) {
                vm.RequestFilterJosn.push({
                    CustomFieldId: elem.CustomFieldId,
                    FieldName: elem.FieldName,
                    ActualDataType: vm.SelectedoperatorMappedDatatype,
                    FilterCount: elem.FilterCount,
                    OperatorExpression: operatoerExpressionResult,
                    OPERATOR_ID: vm.operator_id,
                    Value1: vm.FirstOperandValue1,
                    Value2: vm.SecondOperandValue1,
                    IsMandatoryforFilter: elem.IsMandatoryforFilter,
                    FieldIcons: elem.FieldIcons,
                    DisplayName: elem.DisplayName,
                    FilterTypeAndorOr: elem.FilterTypeAndorOr,
                    PickListOption: elem.PickListOption,
                    ColumnFieldDataCode: elem.ColumnFieldDataCode,
                })
            }
            else {
                let element = vm.RequestFilterJosn.find(item => item.CustomFieldId === elem.CustomFieldId);
                element.OperatorExpression = operatoerExpressionResult;
                element.PickListOption = elem.PickListOption;
                element.ColumnFieldDataCode = elem.ColumnFieldDataCode;
                element.OPERATOR_ID = vm.operator_id;
                element.Value1 = vm.FirstOperandValue1;
                element.Value2 = vm.SecondOperandValue1;
                element.IsMandatoryforFilter = elem.IsMandatoryforFilter;
            }
            vm.ClosePopFilter();
        },
        FillFiltervalue: function (id, element) {
            var vm = this;
            vm.ArrayforFilterpopup.push(element);
            var checkIfExists = vm.ArrayforFilterpopup.findIndex((x) => {
                return x.OPERATOR_ID == id
            });
            if (checkIfExists > -1) {
                var filtervalueexist = vm.RequestFilterJosn.find(x => x.OPERATOR_ID == id && x.CustomFieldId === element.CustomFieldId)
                if (element.ColumnFieldDataCode == 'string') {
                    vm.ValueDisplaytext = filtervalueexist?.Value1;
                    // vm.ValueDisplaytext = element.valueTypeText
                    vm.ValueDisplaytext2 = filtervalueexist?.Value2;
                }
                else if (element.ColumnFieldDataCode == 'decimal' || element.ColumnFieldDataCode == 'int' || element.ColumnFieldDataCode == 'bigint') {
                    vm.ValueDisplaynumber = filtervalueexist?.Value1;
                    vm.ValueDisplaynumber2 = filtervalueexist?.Value2;
                }
                else if (element.ColumnFieldDataCode === 'date') {
                    vm.ValueDisplaydate = filtervalueexist?.Value1;
                    // vm.ValueDisplaydate = element.valueTypeFirstdate;
                    vm.ValueDisplaydate2 = filtervalueexist?.Value2;
                }
                else if (element.ColumnFieldDataCode === 'time') {
                    vm.ValueDisplayTime = filtervalueexist?.Value1;
                    // vm.ValueDisplayTime = element.ValueDisplayFirstTime;
                    vm.ValueDisplayTime2 = filtervalueexist?.Value2;
                }
                else if (element.ColumnFieldDataCode === 'select' && element.PickListOption === 'Lookup') {
                    if (vm.CurrentOperatorSelected == 'IN' || vm.CurrentOperatorSelected == 'NOT IN') {
                        var multiddl = filtervalueexist?.Value1.replace(/"|'/g, '');
                        vm.dropdwonvalues = multiddl?.split(',');
                        var fillmultiselect = [];
                        vm.dropdwonvalues.forEach(dditem=>{
                            const ifexist = vm.options.find(j => j.value === dditem)
                            if (ifexist != -1 && ifexist != undefined) {
                                var fillvalues = {value: ifexist.value, label: ifexist.name}
                                fillmultiselect.push(fillvalues);
                            }
                        })
                        vm.FormSchema[0].Data[0].value = fillmultiselect;
                        vm.dropdwonvalues = [];
                    } else {
                        vm.modelFilterDDLValue = filtervalueexist?.Value1;
                    }
                }
            }
            vm.filterRulechange()
        },
        applySetting: function (CustomFieldId) {
            var vm = this;
            var isBetweenTrue = false
            if(vm.FormSchema[0].Data[0].value.length > 0){
                vm.FormSchema[0].Data[0].value.forEach(multiitem=>{
                    vm.dropdwonvalues.push(multiitem.value)
                })
            }
            if (vm.savedOperatorName == 'Between') {
                if (vm.ValueDisplaytext != '' && vm.ValueDisplaytext2 != '' || (vm.ValueDisplaynumber != '' || vm.ValueDisplaynumber == '0') && vm.ValueDisplaynumber2 != ''
                    || vm.ValueDisplaydate != '' && vm.ValueDisplaydate2 != '' || vm.ValueDisplayTime != '' && vm.ValueDisplayTime2 != '') {
                    isBetweenTrue = true;
                }
            } else if (vm.ValueDisplaytext != '' || vm.ValueDisplaytext2 != '' || vm.ValueDisplaynumber != '' || vm.ValueDisplaynumber == '0' ||
                vm.ValueDisplaynumber2 != '' || vm.ValueDisplaydate != '' || vm.ValueDisplaydate2 != '' || vm.ValueDisplayTime != '' || vm.ValueDisplayTime2 != '' ||
                vm.dropdwonvalues.length > 0 || vm.modelFilterDDLValue != '' || vm.savedOperatorName == 'Is Empty' || vm.savedOperatorName == 'Is Not Empty'
                || vm.savedOperatorName == 'Is Off Day' || vm.savedOperatorName == 'Is Week End') {
                isBetweenTrue = true
            }
            setTimeout(function () {
                if (isBetweenTrue) {
                    var Multiddlvalue = [];
                    // if(vm.ValueDisplaytext=="" && vm.ValueDisplaytext2==""){
                    //     vm.ShowAlert("Please fill this value.", "warning")
                    //     return false;
                    // }
                    vm.dropdwonvalues.forEach(ddlvalues => {
                        const isddlvalueexist = vm.options.find(j => j.value === ddlvalues)
                        if (isddlvalueexist != -1 && isddlvalueexist != undefined) {
                            Multiddlvalue.push(isddlvalueexist.name);
                        }
                    })
                    if (vm.dropdwonvalues != '') {
                        vm.commaSepratedSelectedValues = vm.dropdwonvalues?.join();
                        vm.modelFilterDDLValue = '';
                    }
                    var GetDDLValue = '';
                    if (vm.modelFilterDDLValue != '') {
                        GetDDLValue = vm.options.find(i => i.value === vm.modelFilterDDLValue)
                    }
                    var elem = vm.Filterlist.find(x => x.CustomFieldId == CustomFieldId);
                    elem.OperatorDisplayName = vm.savedOperatorName;
                    elem.valueTypeText = vm.ValueDisplaytext;
                    elem.valueTypeText2 = vm.ValueDisplaytext2;
                    elem.valueTypeNumber = vm.ValueDisplaynumber;
                    elem.valueTypeNumber2 = vm.ValueDisplaynumber2;
                    elem.valueTypeFirstdate = vm.ValueDisplaydate;
                    elem.valueTypeSeconddate = vm.ValueDisplaydate2;
                    elem.ValueDisplayFirstTime = vm.ValueDisplayTime;
                    elem.ValueDisplaySecondTime = vm.ValueDisplayTime2;
                    elem.Displaymultiplevalue = Multiddlvalue?.join();
                    elem.DropDownValue = GetDDLValue?.name;
                    // elem.DropDownValue = GetDDLValue;
                    elem.OPERATOR_ID = vm.operator_id;
                    if (vm.SelectedoperatorMappedDatatype == 'select') {
                        if (vm.CurrentOperatorSelected == 'IN' || vm.CurrentOperatorSelected == 'NOT IN') {
                            //multi select  IN("'1','2','3'") in Filter Case
                            let valArray = vm.commaSepratedSelectedValues.split(".");
                            let commsplitedVal = valArray.join(",")
                            vm.FirstOperand = '\'' + commsplitedVal.split(',').join('\',\'') + '\'';
                            let CommaSepratedStringValues = vm.FirstOperand;
                            vm.FirstOperandValue1 = CommaSepratedStringValues;
                        }
                        else {
                            vm.FirstOperand = '\'' + vm.modelFilterDDLValue + '\'';
                            vm.FirstOperandValue1 = vm.modelFilterDDLValue;
                        }
                    }
                    else if (vm.SelectedoperatorMappedDatatype == 'date') {
                        if (elem.OperatorDisplayName == 'Between') {
                            vm.FirstOperand = '\'' + elem.valueTypeFirstdate + '\'';
                            vm.FirstOperandValue1 = elem.valueTypeFirstdate;
                            vm.SecondOperand = '\'' + elem.valueTypeSeconddate + '\'';
                            vm.SecondOperandValue1 = elem.valueTypeSeconddate;
                        } else {
                            vm.FirstOperand = '\'' + elem.valueTypeFirstdate + '\'';
                            vm.FirstOperandValue1 = elem.valueTypeFirstdate;
                        }
                    } else if (vm.SelectedoperatorMappedDatatype == 'time') {
                        if (elem.OperatorDisplayName == 'Between') {
                            vm.FirstOperand = '\'' + elem.ValueDisplayFirstTime + '\'';
                            vm.FirstOperandValue1 = elem.ValueDisplayFirstTime;
                            vm.SecondOperand = '\'' + elem.ValueDisplaySecondTime + '\'';
                            vm.SecondOperandValue1 = elem.ValueDisplaySecondTime;
                        } else {
                            vm.FirstOperand = '\'' + elem.ValueDisplayFirstTime + '\'';
                            vm.FirstOperandValue1 = elem.ValueDisplayFirstTime;
                        }
                    }
                    vm.FilterJosnRquest(elem);
                } else {
                    vm.ShowAlert("Cannot Add Empty Filter", "failure");
                }
            }, 500)
            //vm.ClosePopFilter();
        },
        FilterTabClicked() {
            var vm = this;
            vm.showModuleListForFilter = true;
            vm.showModuleListForField = false;
            vm.MainSearchmodel = '';
            vm.searchColumnfilter = [];

        },
        FieldTabClicked() {
            var vm = this;
            vm.showModuleListForFilter = false;
            vm.showModuleListForField = true;
            vm.MainSearchmodel = '';
            vm.searchColumnfilter = [];
        },
        openActionIcon: function (e) {
            var vm = this;
            vm.showRowLevelFunction=true;
            // e.target.nextSibling.removeAttribute('style');
            // e.target.nextSibling.setAttribute('style', 'width:240px;');
            // e.target.nextSibling.style.display = 'block';
        },
        closeActionIcon: function (e) {
            var vm = this;
            vm.showRowLevelFunction=false;
        },
        OpenCalculatedUI: function () {
            var vm = this;
            vm.showcalculatedUI = true;
            vm.editexpressionvalue = false;
            vm.showRowLevelFunction=false;
            vm.AlreadyExistExpression = {};
            $("#showcalculatedfield").modal("show");
            setTimeout(function () {
                $("#showcalculatedfield").modal('show');
                setTimeout(function () {
                    vm.SetBootstraptoolTip();
                }, 200);
            }, 1000);
        },
        VerifiedExpressionColumnValue({ ExpressionID, FieldValue, VerifiedExpression, ExpressionFieldArray, ExpressionDisplay, Functionkey ,IsEdit,IsExpExist}) {
            debugger
            var vm = this;
            const checkAlredyExist = vm.Columnlist.findIndex(object => object.CustomFieldId === ExpressionID);
            const checkFieldVlaueExist = vm.Columnlist.findIndex(object => object.FieldName === FieldValue);
            var ExpressionDisplayOrder;
            if ((checkAlredyExist === -1 || checkFieldVlaueExist === -1) && !IsEdit) {
                var checkExpExist = '';
                var indexvalue = '';
                var count = 0;
                vm.Columnlist.forEach(CheckItem => {
                    if (CheckItem.Calculated_Field || count != 0) {
                        vm.setvalue = 1;
                        count++;
                    } else { vm.setvalue = 0; }
                })
                if (vm.setvalue == 1) {
                    vm.Columnlist.forEach(item => {
                        if (item.Calculated_Field) {
                            var tempdata = [];
                            tempdata.push(item);
                            var checkExpvalue = tempdata.find(x => x.ModuleIndex);
                            checkExpExist = checkExpvalue.ModuleIndex;
                        }
                    })
                    indexvalue = parseInt(checkExpExist) + 1;
                }
                else {
                    const getlastarrayvalue = vm.Columnlist[vm.Columnlist.length - 1];
                    indexvalue = getlastarrayvalue.ModuleIndex + 2;
                    vm.setvalue = 1;
                }
                if (vm.Columnlist.length < 1) {
                    ExpressionDisplayOrder = vm.Columnlist.length
                }
                else {
                    const getlastarrayvalue = vm.Columnlist[vm.Columnlist.length - 1];
                    ExpressionDisplayOrder = getlastarrayvalue.Display_Order + 1;
                }
                vm.Columnlist.push({
                    CustomFieldId: '', FieldName: FieldValue, DisplayName: FieldValue, DateFormat: null,
                    DateFormatId: null,
                    DecimalPlace: null,
                    IsUseSeprator: null,
                    IsCurrencySymbol: null,
                    ColumnFieldDataCode: "string",
                    Display_Order: ExpressionDisplayOrder,
                    ModuleIndex: indexvalue,
                    Calculated_Field: true,
                    ExpressionDisplay: ExpressionDisplay
                });
                if (VerifiedExpression != "") {
                    vm.ExpressionArray.push(
                        {
                            "ExpressionID": ExpressionID, "Expression": VerifiedExpression, "ExpressionName": FieldValue, "ExpressionDisplay": ExpressionDisplay, "ColumnIndex": indexvalue,
                            "REPORTID": vm.customreportparameters.ReportID,
                            "FunctionKeys": Functionkey
                        }
                    )
                    var dataele = vm.Columnlist.find(x => x.FieldName === FieldValue);
                    dataele.ModuleIndex = indexvalue;
                    if (vm.ExpressionFieldsArray == undefined) {
                        vm.ExpressionFieldsArray = [];
                    }
                    ExpressionFieldArray.forEach(Arrayitem => {
                        vm.ExpressionFieldsArray.push({ CustomFieldId: Arrayitem.CustomFieldId, FieldName: Arrayitem.FieldName, ColumnIndex: indexvalue });
                    })
                }
            }
            else {
                var colelem = vm.Columnlist.find(x=>x.ModuleIndex === IsExpExist.ModuleIndex)
                colelem.CustomFieldId = ExpressionID;
                colelem.FieldName = FieldValue;
                colelem.DisplayName = FieldValue;
                colelem.DateFormat = null;
                colelem.DateFormatId = null;
                colelem.DecimalPlace = null;
                colelem.IsUseSeprator = null;
                colelem.IsCurrencySymbol = null;
                colelem.ColumnFieldDataCode = "string";
                colelem.Display_Order = IsExpExist.Display_Order;
                colelem.ModuleIndex = IsExpExist.ModuleIndex
                colelem.Calculated_Field = true,
                colelem.ExpressionDisplay = ExpressionDisplay;

                var verifyelem = vm.ExpressionArray.find(vobject => vobject.ColumnIndex === IsExpExist.ModuleIndex)
                verifyelem.ExpressionID = ExpressionID;
                verifyelem.Expression = VerifiedExpression;
                verifyelem.ExpressionName = FieldValue;
                verifyelem.ExpressionDisplay = ExpressionDisplay;
                verifyelem.ColumnIndex = IsExpExist.ModuleIndex;
                verifyelem.REPORTID = vm.customreportparameters.ReportID;
                verifyelem.FunctionKeys = Functionkey;

                ExpressionFieldArray.forEach(Arrayitem => {
                    var fieldexp = vm.ExpressionFieldsArray.find(fobject => fobject.ColumnIndex === IsExpExist.ColumnIndex)
                    fieldexp.CustomFieldId = Arrayitem.CustomFieldId;
                    fieldexp.FieldName = Arrayitem.FieldName;
                    fieldexp.ColumnIndex = IsExpExist.ModuleIndex;
                })
            }
            vm.$refs.Calculated?.close();
        },
        ListFiltering: function () {
            var vm = this;
            let isFieldActive = vm.$refs.FIELDS.active;
            var input = document.getElementById('searchinput');

            var filterinput = input.value.toUpperCase();
            if (filterinput != null && filterinput != "") {
                $(".multi-collapse").addClass("show");
            }
            else {
                $(".multi-collapse").removeClass("show");
            }
            var searchcol = [];
            var searchfilter = [];
            if (isFieldActive) {
                vm.modulelisting.forEach((item, index) => {
                    vm.modulelisting[index].forEach((indexitem) => {
                        searchfilter.push(indexitem);
                        searchcol = searchfilter.filter(searchitem => {
                            var displayMainColname = (searchitem.DisplayName != null) ? searchitem.DisplayName : searchitem.FieldName;
                            return displayMainColname.replace(/\W/g, "").toLowerCase().includes(filterinput.replace(/\W/g, "").toLowerCase());
                        })
                    })
                    vm.searchColumnfilter[index] = searchcol;
                    searchfilter = [];
                    searchcol = [];
                })
                let count = 0;
                for (let filteritem in vm.searchColumnfilter) {
                    if (vm.searchColumnfilter[filteritem].length > 0) {
                        count++;
                    }
                }
                if (count > 0) {
                    vm.SearchNoRecord = false
                } else { vm.SearchNoRecord = true; }
                return vm.searchColumnfilter;
            }
            else {
                vm.modulelistingforfilter.forEach((item, index) => {
                    vm.modulelistingforfilter[index].forEach((indexitem) => {
                        searchfilter.push(indexitem);
                        searchcol = searchfilter.filter(searchitem => {
                            var displayMainColname = (searchitem.DisplayName != null) ? searchitem.DisplayName : searchitem.FieldName;
                            return displayMainColname.replace(/\W/g, "").toLowerCase().includes(filterinput.replace(/\W/g, "").toLowerCase());
                        })
                    })
                    vm.searchColumnfilter[index] = searchcol;
                    searchfilter = [];
                    searchcol = [];
                })
                let count = 0;
                for (let filteritem in vm.searchColumnfilter) {
                    if (vm.searchColumnfilter[filteritem].length > 0) {
                        count++;
                    }
                }
                if (count > 0) {
                    vm.SearchNoRecord = false
                } else { vm.SearchNoRecord = true; }
                return vm.searchColumnfilter;
            }
        },
        checkMove(e) {
            var vm = this;
            var getindex = 0;
            if (e.related.id == 'TargetDivID') {
                if(!vm.showModuleListForFilter){
                    if(vm.Columnlist.length < 20 || e.dragged.id === e.related.id){
                        return true;}
                    else{
                        vm.ShowAlert(vm.$t("AddCustomFields"), 'failure', true, vm.$t('Alert'));
                        return false;
                    }
                }
                else{
                    if(vm.Filterlist.length < 20){
                        return true;}
                    else{
                        vm.ShowAlert(vm.$t("AddCustomFilter"), 'failure', true, vm.$t('Alert'));
                        return false;
                    }
                }
            }
            else if (!e.draggedContext.element.Calculated_Field && e.draggedContext.element.Calculated_Field != undefined) {
                vm.modulelisting.forEach((i, index) => {
                    vm.GetSubModuleList[index].forEach(j => {
                        if (e.relatedContext.list.length == 0) {
                            if (j.CustomFieldId === e.draggedContext.element.CustomFieldId) {
                                getindex = index;
                            }
                        }
                        else if (j.CustomFieldId === e.relatedContext.element.CustomFieldId) {
                            getindex = index;
                        }
                    })
                })
                if (getindex === e.draggedContext.element.ModuleIndex) {
                    return true;
                } else {
                    return false;
                }
            }
            else {
                return false;
            } 
        },
        onDragEnd: function (e, x) {
            if (e.item.id == 'StopDragfield') {
                return true;
            }
            else {
                e.preventDefault();
                return false;
            }
        },
        ColumnListDisplayOrder: function (e) {
            var vm = this;
            if(!vm.showModuleListForFilter){
                $(".setting-div-box").width('0px');
                vm.Columnlist.forEach((Colitem, Colindex) => {
                    Colitem.Display_Order = Colindex + 1;
                })
            }else{
                vm.dragover();
                vm.ClosePopFilter();
            }
        },
        GetDraggedItem(event, index, MainFieldname) {
            var vm = this;
            if (event.removed != undefined) {
                if (MainFieldname != undefined) {
                    let GetAddedFieldFromDrag = vm.Columnlist.findIndex(AddedField => AddedField.CustomFieldId === event.removed.element.CustomFieldId);
                    let GetAddedFieldFromModuleListing = vm.modulelisting[index].findIndex(ModuleListField => ModuleListField.CustomFieldId === event.removed.element.CustomFieldId);
                    if (GetAddedFieldFromModuleListing != -1) {
                        vm.modulelisting[index].splice(vm.modulelisting[index].indexOf(event.removed.element), 1);
                    }
                    if (GetAddedFieldFromDrag != -1) {
                        vm.Columnlist.splice(GetAddedFieldFromDrag, 1);
                        vm.Addfields(event.removed.element, event, index, MainFieldname);
                    }
                } else {
                    let GetAddedFieldFromDrag = vm.Filterlist.findIndex(AddedField => AddedField.CustomFieldId === event.removed.element.CustomFieldId);
                    let GetAddedFieldFromModuleListing = vm.modulelistingforfilter[index].findIndex(ModuleListField => ModuleListField.CustomFieldId === event.removed.element.CustomFieldId);
                    if (GetAddedFieldFromModuleListing != -1) {
                        vm.modulelistingforfilter[index].splice(vm.modulelistingforfilter[index].indexOf(event.removed.element), 1);
                    }
                    if (GetAddedFieldFromDrag != -1) {
                        vm.Filterlist.splice(GetAddedFieldFromDrag, 1);
                        vm.AddFilter(event.removed.element, index);
                    }
                }
            }
        },
        MatchIfSameNameExist() {
            var vm = this;
            for (let i = 0; i < vm.Columnlist.length; i++) {
                for (let j = 0; j < i; j++) {
                    if (vm.Columnlist[i].CustomFieldId != vm.Columnlist[j].CustomFieldId && (vm.$t(vm.Columnlist[i].DisplayName) == vm.$t(vm.Columnlist[j].DisplayName)
                    || vm.$t(vm.Columnlist[i].DisplayName).toLowerCase() == vm.$t(vm.Columnlist[j].DisplayName).toLowerCase())) {
                        return vm.$t(vm.Columnlist[i].DisplayName);
                    }
                }
            }
            return false;
        },
        filterRulechange() {
            var vm = this;
            vm.filtertype = '';
            if (vm.category == 'Match All Value') {
                vm.Filterlist.forEach(Fitem => {
                    Fitem.FilterTypeAndorOr = 'AND'
                })
                if (vm.RequestFilterJosn.length > 0) {
                    vm.RequestFilterJosn.forEach(RFitem => {
                        RFitem.FilterTypeAndorOr = 'AND'
                    })
                }
                vm.filtertype = 'AND'
            }
            else {
                vm.Filterlist.forEach(Fitem => {
                    Fitem.FilterTypeAndorOr = 'OR'
                })
                if (vm.RequestFilterJosn.length > 0) {
                    vm.RequestFilterJosn.forEach(RFitem => {
                        RFitem.FilterTypeAndorOr = 'OR'
                    })
                }
                vm.filtertype = 'OR'
            }
        },
        CheckForValidSorting(sortColummnarray) {
            var vm = this;
            var count = 0;
            sortColummnarray.forEach(sortitem => {
                if (sortitem.sortcolummnId != '' && sortitem.columnOrders == '' || sortitem.sortcolummnId == '' && sortitem.columnOrders != '' || sortitem.sortcolummnId == undefined || sortitem.columnOrders == undefined) {
                    count = 1;
                }
            })
            return count;
        },
        PreventValues(event) {
            var vm = this;
            var ekey = event.keyCode;
            if (ekey == 69 || ekey == 107 || ekey == 109) { event.preventDefault(); }
        },
        PreventDecimal(event) {
            var ekey = event.keyCode;
            if (ekey == 110 || ekey == 190) { event.preventDefault(); }
        },
        TruncateStr: function (str, len) {
            var convertstring = '' + str;
            if (convertstring != null)
                if (convertstring.length <= len) return str;
                else return convertstring.substring(0, len) + "...";
        }
    }
}


</script>
<template>
    <section class="page-action">
        <div class="left-menu-tab datareport">
            <div :class="maindiv">
                <loader :isVisible="isLoading"></loader>
                <div class="accordion md-accordion left-filter" id="accordionEx" role="tablist" aria-multiselectable="true">
                    <div :class="editclass">
                        <div class="card-header filter-head p-0" role="tab" id="headingOne1">
                            <h5 class="mb-0 px-2">{{ ReportName }}
                                <span class="expand_all_filters">
                                    <span id="SearchData"><a :title="$t('EditReportName')" v-on:click="editreport()"
                                            data-toggle-tooltip="tooltip" class="collapsed"> <em aria-hidden="true"
                                                class="fa fa-pencil" style="font-size:20px;"></em></a></span>
                                    <span>
                                        <a title="Collapse" v-on:click="collapsedclick()" data-toggle-tooltip="tooltip"
                                            class="collapsed"><em aria-hidden="true"
                                                class="fa fa-angle-double-left mt-1"></em>
                                        </a>
                                    </span>
                                </span>
                            </h5>
                        </div>
                    </div>
                    <div :class="saveclass">
                        <div class="card-header filter-head p-0" role="tab" id="headingOne1">
